import Constants from "../std/Constants.js";
import Util      from "../std/Util.js";
import Orbit     from "../orbits/Orbit.js";
import ssData    from "../data/PlanetaryData.js";

class SolarPlanets {
    
    
    /** Get the elements for a planet at a specific time. */
    static getElements(name, time) {
        
        // Convert t from seconds to centuries...
        const t = time/Constants.CENTURY;
        const elems = {};
        // Remember to get the planets of Sol.
        const p = ssData.Sol.planets[name];
        elems.a     = (p.elems.a + t*p.derivs.a)*Constants.AU;
        elems.Eccen =  p.elems.e + t*p.derivs.e;
        // Defer conversion to radians.
        var   ML    =  p.elems.ML + t*p.derivs.ML;
        elems.i     = Util.toRadians(p.elems.i     + t*p.derivs.i);
        var   LonP  = Util.toRadians(p.elems.LonP  + t*p.derivs.LonP);
        elems.Omega = Util.toRadians(p.elems.Omega + t*p.derivs.Omega);
        
        elems.Per   = 360./p.derivs.ML * Constants.CENTURY;
        
        elems.nu    = LonP - elems.Omega;
        
        let MA      = Util.toRadians(ML) - LonP;
        if (p.corr != null) {
            let corr =  p.corr[0]*t*t + 
                        // Paper doesn't explicitly indicate that the trig functions
                        // are in degrees, but in other cases in paper they clearly are.
                        p.corr[1]*Math.cos(Util.toRadians(p.corr[3]*t)) +
                        p.corr[2]*Math.sin(Util.toRadians(p.corr[3]*t));
             MA += Util.toRadians(corr);
        }
        MA = MA % (2*Math.PI);
        
        elems.t0    = time - MA/(2*Math.PI) * elems.Per;
        let ringAngle = null;
        let ringColor = null;
        if (p.ring) {
            ringAngle = p.ring.ringAngle;
            ringColor = p.ring.color;
        } 
        return {elements: elems, mass: p.mass, diameter: 2*p.radius/1000, color: p.render.color, ringColor: ringColor, ringAngle: ringAngle};
    }
        
    // Get the orbit for a planet at a given time.
    static getOrbit(pname, time) {
        const planetData = SolarPlanets.getElements(pname, time);
        const elems = planetData.elements;

        const orb = new Orbit(pname);

        for (var key in elems) {
            orb.addParam(key, elems[key]);
        }
        orb.addParam("t", time);
        // Make sure we can get positions.
        const r0 = orb.getParameter("r", true);
        if (r0 == null) {
            return null;
        }
        return orb.quickOrbit();
    }
}

export default SolarPlanets;