import {Color, BufferGeometry, PointsMaterial, Float32BufferAttribute, Vector3} from "three";
import AngPoints from "../three/AngPoints.js";
import Util from "../std/Util.js";
import Dump from "../std/Dump.js";

const SmallBodies = function(smbData, colorLookup, startTime) {
    let xcount = 0;
    function getPositions(data, time) {
        xcount += 1;
        const px = [];
        for (var i=0; i<data.length; i += 1) {
            if (data[i].orbit == null) {
                Util.log("No orbit for "+i+" "+data.name);
            }
            const p = data[i].orbit.position(time);
            px.push(p[0], p[1], p[2]);
        }
        return new Float32BufferAttribute(px,3);
    }
    
    function getColors(data, colorLookup) {
        
            var allTypes = {
"A":              0xbfef45,
"Amor":           0x1f8231,
"Apollo":         0xfabed4,
"Aten":           0xdcbeff,
"Atira":          0x469990,
"C":              0xf032e6,
"Distant Object": 0x3cb44b,
"Hilda":          0xffd8b1,
"Hungaria":       0xaaffc3,
"I":              0x9a6324,
"Jupiter Trojan": 0x42d4f4,
"MBA":            0xffe119,
"Object with perihelion distance < 1.665 AU": 0x4363d8,
"P":              0xe6194b,
"Phocaea":        0xfffac8
    };
        const colors = [];
        
        for (var i=0; i < data.length; i += 1) {
            const t   = data[i].Orbit_type;
            const h   = data[i].H;
            var col = 0xFFFFFF;
            if (allTypes[t]) {
                col = allTypes[t];
            } else {
                Util.log("Cannot find type for: "+i);
            }
            const c3 = new Color(col);
            var fac = 1;
            if (t !== "P") {
                fac = 0.2;
            }
            if (h > 5) {
                fac = 5/h;
            }
            colors.push(c3.r*fac, c3.g*fac, c3.b*fac);
            
        }
        return new Float32BufferAttribute( colors, 3 );

    }


    const geometry = new BufferGeometry();        
    // Should the time here be 0?
    geometry.setAttribute( 'position', getPositions(smbData, startTime));
    geometry.setAttribute( 'color',    getColors(smbData, colorLookup));
        
    const material       = new PointsMaterial( {sizeAttenuation: false, size: 2, vertexColors: true } );
    const points         = new AngPoints(geometry, material );
    
    // Add this function to the component.
    points.getIndexedMeta = function(index) {
        let meta = {...smbData[index]};
        return meta;
    };
    
    return {
        component: points,
        render:function(time) {
                  geometry.setAttribute( 'position', getPositions(smbData, time));
        }
    };
}

export default SmallBodies;