import Constants   from "../std/Constants.js";
import Util        from '../std/Util.js';
import Parameter   from './Parameter.js';
import LoadOrigins from './Origins.js';

class ParameterFactory {
    

    /** This is the map of all the legal parameters.  The fiducial name
     *  points to the name, dimensionality,
     *  and description, and indicates if the parameter is a constant of the motion
     *  or varies along the orbit.
     */
    static legalParameters = {};
    
    /** This is a mapping to the fiducial capitalization of the parameter.
     *  it maps String.toLowerCase() -> String.fiducial. 
     */
    static caseConv        = {};
    
    
    /** This is the mapping between fiducial names and arrays of generators to
     *  create them.
     */
    static generators       = null;
    
    static constants        = [];
    static variables        = [];
    
    /** Add a parameter into the list of legal parameters */
    static addParam(name, dim, varies, descrip, units) {
        if (ParameterFactory.caseConv[name.toLowerCase()] != null ) {
            throw new Error("Redundant defintion for parameter name:"+name);
        }
        ParameterFactory.legalParameters[name] = {
            name: name,
            dim: dim,
            varies: varies,
            descrip: descrip,
            units: units
        };
        ParameterFactory.caseConv[name.toLowerCase()] = name;
        if (varies) {
            this.variables.push(name);
        } else {
            this.constants.push(name); 
        }
    }
    
    /** Get the informatino about a parameter */
    static getParamInfo(name) {
        return ParameterFactory.legalParameters[ParameterFactory.fiducial(name)];        
    }
    
    /** Get the list of variable parameters */
    static getVariables() {
        var variables = [];
        for (var name in Object.keys(ParameterFactory.legalParameters)) {
            var p = ParameterFactory.legalParameters[name];
            if (p.varies) {
                variables.push(name);
            }
        }
        return variables;
    }
    
    /** Convert the case of the input to the fiducial name for that parameter */
    static fiducial(name) {
        return ParameterFactory.caseConv[name.toLowerCase()];
    }
   
    /** Check if the name and type of a parameter are consistent */
    static checkName(name, type) {
        var pt = ParameterFactory.getParamInfo(name);
        return (pt != null) && (pt.dim === type);
    }
    

    
    /** Return the list of valid parameter names */
    static getNames() {
        const keys = Object.keys(ParameterFactory.legalParameters);
        return keys;
    }
    
    /** Find the first generator where are prerequisites are met */
    static findGenerator(name, knownParams) {
        
        // Get the array of generators for this parameters.
        const gens = ParameterFactory.generators[name];
        if (gens == null || gens.length === 0) {
            // No generators for this parameter
            return null;
        }
        
        // Loop over the generators ...
        for (var g=0; g<gens.length; g += 1) {
            const gen  = gens[g];
            const reqs = gen.reqs;
            
            var allFound = true;            
            // ... check that we have all needed prerequisites
            for (var r=0; r<reqs.length; r += 1) {
                var req = reqs[r];
                if (!knownParams[req]) {
                    allFound = false;
                    break;
                } 
            }
            if (allFound) {
                return gen;
            }
        }
        return null;            
    }
    
    
    /** Generate the parameter
     *  in terms of other known parameters.
     */
    static deriveParameter(xname, knownParams) {
        const name  = ParameterFactory.fiducial(xname);
        if (name == null) {
            throw new Error("Attempt to create invalid parameter: "+xname);
        }
        
        if (ParameterFactory.generators == null) {
            ParameterFactory.generators = {};
            LoadOrigins();
        }
		
        // Get the array of generators for this parameters.
        const gens = ParameterFactory.generators[name];
        if (gens == null || gens.length === 0) {
            // No generators for this parameter
            return null;
        }
        
        var param = null;
        
        // Loop over the generators ...
        for (var g=0; g<gens.length; g += 1) {
            const gen  = gens[g];
            const reqs = gen.reqs;
            
            var allFound = true;            
            // ... check that we have all needed prerequisites
            for (var r=0; r<reqs.length; r += 1) {
                var req = reqs[r];
                if (!knownParams[req]) {
                    allFound = false;
                    break;
                } 
            }
            
            if (allFound) {
                // ... then generate the parameters from the known parameters.
                param = gen.generate(knownParams);
                
                // Might still fail to generate parameter.
                if (param != null) {
                    break;
                }
            }
            // Otherwise we'll just return a null.
            
        }
        return param;
    }
    
    // Add a generator for the given parameter name, with the 
    // specified prequisites and a function for generating the parameters 
    // from a set of known parameters.
    static addGenerator(xname, reqs, func) {
        if (xname == null || reqs == null || func == null) {
            throw new Error("Invalid generator, null element");
        }
        const name = ParameterFactory.fiducial(xname);
        if (name == null) {
            throw new Error("Adding generator for unknown parameter");
        }
        const gens = ParameterFactory.generators;
        var arr = gens[name];
        if (arr == null) {
            arr        = [];
            gens[name] = arr;
        }
        arr.push({reqs: reqs, generate:func});
    }
    
    
    
    static baseRender = function(inp, unit, called) {
        var add   = 4;  // Show scalars to higher precision
        var max   = 1.e10;
        if (called) {
            add = 0;
            max   = 1.e6;
        }
        
        var str;
        if (Array.isArray(inp)) {
            str = "[ ";
            for (var i=0; i<inp.length; i += 1) {
                str += ParameterFactory.baseRender(inp[i], unit, true)+"  ";
            }
            str += " ] ";
        } else {
            
            if (unit) {
               inp /= unit.scale;
               if (unit.offset) {
                   inp += unit.offset;
               }
            }
            
            const abs = Math.abs(inp);
            if (abs !== 0 && (abs < .001 || abs > max)) {
                str = inp.toExponential(5+add);
            } else {
                str = inp.toPrecision(6+add);
            }
        }
        if (unit && !called) {
            str += " "+unit.unit;
        }
        return str;
    }
    
    // Create a parameter with the given name and value.
    static genParam(name, value, unit) {
        const info = ParameterFactory.getParamInfo(name);
        if (info == null) {
            Util.log("Attempt to generate invalid parameter:"+name);
        }
        return new Parameter(name, value, info.dim, info.varies, ParameterFactory.baseRender, unit);        
    }

}

export default ParameterFactory;
// 
// Load the list of valid parameters.
//                               NAME             DIM             VARIES  DESCRIP
// Constants of the orbit
ParameterFactory.addParam("a",     Parameter.SCALAR, false, "Semimajor axis",
   Constants.radUnits);
ParameterFactory.addParam("b",     Parameter.SCALAR, false, "Semiminor axis",
   Constants.radUnits);
ParameterFactory.addParam("C",     Parameter.VECTOR, false, "The center of the ellipse",
   Constants.radUnits);
ParameterFactory.addParam("E",     Parameter.SCALAR, false, "The energy of the orbit");
ParameterFactory.addParam("Ecc",   Parameter.VECTOR, false, "The eccentricity vector");
ParameterFactory.addParam("Eccen", Parameter.SCALAR, false, "The eccentricity scalar");
ParameterFactory.addParam("eF2",   Parameter.VECTOR, false, "The energy-scaled position vector of the second focus");
ParameterFactory.addParam("G",     Parameter.SCALAR, false, "The gravitational constant (defaults to MKS)");
ParameterFactory.addParam("i",     Parameter.SCALAR, false, "The inclination of the orbit",
   Constants.angUnits);
ParameterFactory.addParam("L",     Parameter.VECTOR, false, "The angular momentum of the orbit");        
ParameterFactory.addParam("LonP",  Parameter.SCALAR, false, "The longitude of periapsis",
   Constants.angUnits);
ParameterFactory.addParam("M",     Parameter.SCALAR, false, "Mass of the central body (defaults to Ms)",
   Constants.massUnits);
ParameterFactory.addParam("m1",    Parameter.SCALAR, false, "Mass of the orbiting body (defaults to 0)",
   Constants.massUnits);
ParameterFactory.addParam("N0",    Parameter.SCALAR, false, "Orbits per day");

ParameterFactory.addParam("nu",    Parameter.SCALAR, false, "Argument of periapsis",
   Constants.angUnits);
ParameterFactory.addParam("Omega", Parameter.SCALAR, false, "The longitude of the ascending node",
   Constants.angUnits);
ParameterFactory.addParam("Per",   Parameter.SCALAR, false, "The period of the orbit",
   Constants.intervUnits);
ParameterFactory.addParam("Pnode", Parameter.VECTOR, false, "The vector pointing the the ascending node");
ParameterFactory.addParam("Rot",   Parameter.BLOB,   false, "Packaged rotation information");
ParameterFactory.addParam("t0",    Parameter.SCALAR, false, "The reference time for the orbit",
   Constants.timeUnits);

// Elements that vary during the orbit (i.e., as a function of time)
ParameterFactory.addParam("EA",    Parameter.SCALAR, true, "The eccentric/hyperbolic anomaly",
   Constants.angUnits);
ParameterFactory.addParam("MA",    Parameter.SCALAR, true, "The mean anomaly",
   Constants.angUnits);
ParameterFactory.addParam("MLon",  Parameter.SCALAR, true, "The mean longitude",
   Constants.angUnits);
ParameterFactory.addParam("r",     Parameter.VECTOR, true, "Position of the object",
   Constants.radUnits);
ParameterFactory.addParam("t",     Parameter.SCALAR, true, "The current time",
   Constants.timeUnits);
ParameterFactory.addParam("TA",    Parameter.SCALAR, true, "The true anomaly",
   Constants.angUnits);
ParameterFactory.addParam("v",     Parameter.VECTOR, true, "Velocity of the object",
   Constants.velUnits);
   
if (!ParameterFactory.generators) {
	LoadOrigins();
}