import Util from "../std/Util.js";
import {Component} from "react";

import ClickSpan       from "../elems/ClickSpan.js";
import OptionsSelector from "../elems/OptionsSelector.js";
import InfoMsg         from "../elems/InfoMsg.js";
import SepBoxes        from "../elems/SepBoxes.js";
import ssData          from "../data/PlanetaryData.js";
import OrbitPlot       from "../solarsystem/OrbitPlot.js";
import Planet          from "../solarsystem/Planet.js";

import Dump from "../std/Dump.js";

import {Vector3} from "three";


class ObjRow extends Component {
    
    constructor(props) {
        super(props);        
        this.anim   = props.anim;
        this.entry  = props.entry;
        this.holder = props.holder;
    }
    
    render() {
        
        let type = this.entry.meta.type;
        let name = this.entry.meta.name;
        let anim = this.anim;
        
        let act  = [];
        
        // Everything gets an info.
        act.push(
            <ClickSpan key='i' type={type} text="i" 
                click={this.info.bind(this)}
                value={this.entry} />
        );

        // Only planets in the solar system and moons with
        // textures in planetary systems have animations.
        if ( (type == "orbit"  && ssData.Sol.planets[name]) ||
             (type == "planet"  && this.entry.meta.orbit) ||
             (type == "moon"    && this.entry.meta.orbit  &&
              this.entry.meta.render.texture) )  {
            act.push(
                <ClickSpan key="a" text="A"
                    value={this.entry} 
                    click={this.subAnim.bind(this)}/>
            );
        }
        
        let skipMission = false;
        // Check if this is a non-active mission
        if (type == "mission") {
            let time = anim.time;
            let miss = ssData.Sol.missions[name];
            skipMission = time < miss.orbits[0].start || time > miss.orbits[miss.orbits.length-1].stop;
        }
        
        // If there is an orbit associated, then we allow it to be
        // drawn so long as it's not a planet, since they already have one drawn
        // We also skip inactive missions.
        if (!skipMission && type != "planet"  && type != "orbit" && this.entry.meta.orbit) {
            act.push(
                <ClickSpan key="o" text="O"
                     value={this.entry}
                     click={this.showOrbit.bind(this)}
                 />
            );
        }
        
        // Allow recentering and delta around anything except an orbit but don't add 
        // option if there is only one body in animation.
        if (type != 'orbit' && anim.bodyCount > 1  && !skipMission) {
            act.push(<ClickSpan key="c" text="C" 
                                value={this.entry} 
                                click={this.centerView.bind(this)}/>);
            act.push(<ClickSpan key="d" text="D" 
                                value={this.entry} 
                                click={this.showSeparation.bind(this)} />);
        }
        return  <li> {name} [{type}]  {act} </li>;
    }
    
    info(input) {
        let msg = InfoMsg(input, this.anim);
        this.anim.setQuickHTML(msg);
    }

    subAnim(entry) {
        if (ssData.Sol.planets[entry.meta.name]) {
            this.anim.setAnim(entry.meta.name);
        } else {
            this.anim.animateMoon(entry.meta.name);
        }
        this.holder.setState({optState: false, content: null});
    }

    centerView(entry) {
    
        let p = null;
        if (entry.name == "Sun"  || entry.name == "Sol") {
            p = [0,0,0];
            
        } else {
            let time = this.anim.time;
            if (entry.meta.type == "star") {
                const scl = time/30.856e12;
                let star = entry.meta;
                p = [star.x+scl*star.u, star.y+scl*star.v, star.z+scl*star.w];
                
            } else if (entry.meta.type == "galaxy") {
                let gal = entry.meta;
                p = [gal.x, gal.y, gal.z];

            } else if (entry.meta.orbit) {
                p = entry.meta.orbit.position(time);
            } else if (entry.component) {
                let pos = entry.component.position;
                p = [pos.x, pos.y, pos.z];
            } else {
                Util.log("Unexpected 0 center for "+entry.meta.name);
                p = [0,0,0];
            }
        }
        let controls = this.anim.frame.controls;
        controls.target = new Vector3(p[0], p[1], p[2]);
        controls.update();
    }

    showOrbit(entry) {
        let orbit = entry.meta.orbit;
        let anim = this.anim;

        let op = OrbitPlot(entry.meta.name, orbit, 0x00FFFF, anim.time);
        op.component.meta = {...entry.meta, type: "orbit", otype: entry.meta.type};
        anim.addElement(entry.meta.name + "Orbit"+OptionsSelector.index, op);
        const planetBody = Planet(entry.meta.name, anim.time, entry.meta.orbit, 4000, 0xFFFF00, true);
        planetBody.component.meta = {...entry.meta};
        anim.addElement(entry.meta.name+"Body"+OptionsSelector.index, planetBody);
        OptionsSelector.index += 1;
    }

    showSeparation(entry) {
        this.holder.setState({mode: "content",
            content: <SepBoxes entry={entry} row={this} anim={this.anim} />}
        );
    }
}
export default ObjRow;
