function Dump(obj, label, maxLevel) {
    function recurse(obj, label, level, maxLev) {
        var indent = "                                        ".substring(0, 4 * level);
        var str = indent;
        if (label) {
            str += label + ":";
        }
        if (obj === null) {
            str += "null";
        } else if (obj === undefined) {
            str += "undefined";
        } else {
            var type = typeof (obj);
            if (type === "string") {
                str += "'" + obj + "'";
            } else if (type === 'number' || type === 'boolean') {
                str += obj;
            } else if (type === 'function') {
                str += "function";
            } else if (type === 'array') {
                str += "[";
                var len = obj.length;
                if (len > this.maxArray) {
                    len = this.maxArray;
                }
                if (level >= maxLev) {
                    len = 0;
                }
                if (len > 0) {
                    str += "\n";
                }
                for (var i = 0; i < len; i += 1) {
                    str += recurse(obj[i], "", level + 1, maxLev) + "\n";
                }
                if (len < obj.length) {
                    str += "...\n";
                }
                if (len > 0) {
                    str += indent;
                }
                str += "]";
            } else {
                // Object
                str += "{";
                if (level < maxLev) {
                    str += "\n";
                    var keys = Object.keys(obj);
                    keys.sort();
                    for (var i = 0; i < keys.length; i += 1) {
                        var zz = keys[i];
                        str += recurse(obj[zz], zz, level + 1, maxLev) + "\n";
                    }
                    str += indent;
                } else {
                    str += "...";
                }
                str += "}";
            }
        }
        return str;
    }

    if (!maxLevel) {
        maxLevel = 2;
    }
    console.log(recurse(obj, label, 0, maxLevel));
}

export default Dump;


