import Animation from "../elems/Animation";

import loadStars from "../stars/StarReader";

import PrepStar      from "../stars/PrepStar";
import Util      from "../std/Util";
import Constants from "../std/Constants";

import TextInput from "../elems/TextInput";

import StarFinder  from "../stars/StarFinder";

import {Scene, Vector3, InstancedMesh, 
        SphereGeometry, RingGeometry, MeshBasicMaterial, 
        BufferGeometry, LineBasicMaterial, Line,
        Matrix4, Color} from "three";

class LocalGalaxy extends Animation {
    
    fields = {
        timeID:  "timeDisp",
        name:    "Local Galactic Neighborhood",
        delta:   Constants.CENTURY/20,
        time:    0,
        centerBody: "Sun",
        baseAnimation: "Nearby Galaxies",
        longClickTime: 1500,
        paused:  true,
        sceneScale: 'pc',
        sceneCoord: "Ga"
    };


    constructor(props) {
        super(props);
        
        // Add local defaults
        for (var name in this.fields) {
            this[name] = this.fields[name];
        }
        
        // Add any properties
        for (var name in props) {
            this[name] = props[name];
        }
        
        if (this.timeID) {
            this.timeElem = document.getElementById(this.timeID);
            this.timeElem.innerHTML = this.timeFormatter(this.time);
        }
        this.scene     = new Scene();
        this.starFinder = null;
        
        // Used so that we can delete the lastLine on
        // mouseup or a new click.
        
        this.savedLine = null;

        this.loading    = true;
        Util.log("Loading stars...");
        loadStars(this.settings, this.finishConstruction.bind(this));
    }
    
    finishConstruction(starData) {
        Util.log("Stars loaded:"+starData.length);
        
        this.starData = starData;
        let ringIndices = [];
        for (let i=0; i<starData.length; i += 1) {
            let star = starData[i];
            PrepStar(star);
            if (star.ring) {
                ringIndices.push(i);
            }
        }
        
        const material = new MeshBasicMaterial({wireframe: false});
        const baseStar = new SphereGeometry(1.0, 20, 20);
        const baseRing = new RingGeometry(1.1,1.4,20);
        
        const theStars = new InstancedMesh(baseStar, material, starData.length);
        for (let i=0; i<starData.length; i += 1) {
            let star = starData[i];
            let mat = new Matrix4().makeScale(star.size, star.size, star.size);
            mat.setPosition(star.x,star.y,star.z);
            theStars.setMatrixAt(i, mat);
            theStars.setColorAt(i, new Color(star.color));
        }
        
        const theRings = new InstancedMesh(baseRing, material, ringIndices.length);
        for (let r=0; r<ringIndices.length; r += 1) {
            let i = ringIndices[r];
            let star = starData[i];
            let mat = new Matrix4().makeScale(star.size, star.size, star.size);
            mat.setPosition(star.x,star.y,star.z);
            theRings.setMatrixAt(r, mat);            
            theRings.setColorAt(r, new Color(star.ring.color));
        }
        
        theStars.instanceColor.needsUpdate = true;
        theRings.instanceColor.needsUpdate = true;
        this.ringIndices = ringIndices;
        // Add this function to the component.
        theStars.getIndexedMeta = function(index) {
            let star = this.starData[index];
            let meta = {...this.starData[index], type:"star"};
            return meta;
        }.bind(this);
        
        theRings.getIndexedMeta = function(index) {
            let meta = {...this.starData[this.ringIndices[index]], type:"star"};
            return meta;
        }.bind(this);
        
        this.addElement("AllStars", {name:"AllStars", component: theStars,
           render: function(time) {
               const scl = time/30.856e12;
               let mat = new Matrix4();
               for (let i=0; i<starData.length; i += 1) {
                   theStars.getMatrixAt(i, mat);
                   let star = starData[i];
                   mat = mat.setPosition(star.x+scl*star.u, star.y+scl*star.v, star.z+scl*star.w);
                   theStars.setMatrixAt(i, mat);
               }
               theStars.instanceMatrix.needsUpdate = true;                   
           }});
       
        this.addElement("AllRings", {name:"AllRings", component: theRings,
            render: function(time) {
               const scl = time/30.856e12;
               let mat = new Matrix4();
               for (let r=0; r<ringIndices.length; r += 1) {
                   theRings.getMatrixAt(r, mat);
                   let i = ringIndices[r];
                   let star = starData[i];
                   mat = mat.setPosition(star.x+scl*star.u, star.y+scl*star.v, star.z+scl*star.w);
                   theRings.setMatrixAt(r, mat);
               }  
               theRings.instanceMatrix.needsUpdate = true;                   
           }});
        
        
        this.loading = false;
        this.addTimeDisplay();
        Util.log("Local Galactic Neighborhood:");
        Util.log("   Number of stars rendered:"+starData.length);
        Util.log("   Number of planetary systems:"+ringIndices.length);
    }
    
    
    timeFormatter(time) {
        let yr = Math.floor((100*time/Constants.CENTURY + 2000).toFixed(0));
        if (yr > 0) {
            return yr+" AD";
            
        } else {
            // No year 0.
            yr -= 1;
            yr  = -yr;
            return yr + " BC";
        }
    }
        
    find(name, callback) {
        if (!this.starFinder) {
            this.starFinder = new StarFinder(this.starData);
        }
        this.starFinder.getStar(name, callback);        
    }
    
    position(star, time) {
        
        var v;
        const scl = time/30.856e12;

        if (star.u == null || star.v == null || star.w == null) {
            v = [0,0,0];
        } else {
            v = [scl*star.u, scl*star.v, scl*star.w];
        }
        return new Vector3(
            star.x+v[0], star.y+v[1], star.z+v[2]);            
    }
    
    parseTime(year) {
        if (!isNaN(year)) {
            return (year-2000)/100 * Constants.CENTURY;
        }  else {
            return 0;
        }
    }
    doLongClick(e) {
        const matched = this.frame.raycast(e);
        let found = null;
        
        for (let i=0; i<matched.length; i += 1) {
            let entry = matched[i];
            let name = entry.meta.name;
            if (name == "Sun" || name == "Sol") {
                this.setAnim("Solar System");
                return;
            }
        }
        super.doLongClick(e);
    }
    

}

export default LocalGalaxy;