
import Util from "../std/Util.js";
import Dump from "../std/Dump.js";

function colorChar(spt) {
    if (spt == null) {
        return null;
    }
    if (spt.length > 2) {
        if (spt.startsWith("sd")) {
            spt = spt.substring(2);
        }
    }
    if (spt.length > 1) {
        if (spt.startsWith("D")  || spt.startsWith("d")) {
            spt = spt.substring(1);
        }
    }
    return spt.charAt(0);
}

function getSize(spt) {
    
    var sizes = {
        "M": .15,
        "K": .2,
        "G": .25,
        "F": 0.3,
        "A": 0.35,
        "B": 0.4,
        "O": 0.45,
        "T": 0.10,
        "Y": 0.10,
        "W": 0.45
    }
    if (!spt) {
        return 0.01;
    }
            
    let factor = 1;
    if (spt.startsWith("sd")  || spt.startsWith("d")) {
        factor *= 0.8;
    }
    
    if (spt.startsWith("D")) {
        factor *= 0.5; 
    }
    
    let cc = colorChar(spt);
    var size = 0.1;
    
    if (sizes[cc]) {
        size = sizes[cc];
        size *= factor;
        if (spt.indexOf("IV") > 0) {
            size *= 1.3;
        } else if (spt.indexOf("III")) {
            size *= 1.5;
        } else if (spt.indexOf("II")) {
            size *= 1.7;
        } else if (spt.indexOf("I")) {
            size *= 2.0;
        }
    }
    
    return size * .15;
}

function getColor(spt) {

    var SptColors = {
        "M": 0xff3300,
        "K": 0xffaa66,
        "G": 0xffff00,
        "F": 0xffffaa,
        "A": 0xffffff,
        "B": 0xaaaaff,
        "D": 0x00ff00,
        "O": 0x0000ff,
        "s": 0x00ff00,
        "W": 0x0000FF,
        "T": 0xEADDCA,
        "Y": 0xEADDCA
    };
    
    let col = 0x00FFFF;
    let cc = colorChar(spt);
    if (cc && SptColors[cc]) {
        return SptColors[cc];
    }

    return col;
}

// Normally the size and color are derived
// from the spectral type, but user can
// supply it directly.
const PrepStar = function(star){
    if (Array.isArray(star.x)) {
        star.y = star.x[1];
        star.z = star.x[2];
        star.x = star.x[0];  // Last to avoid premature overwrite
    }
    if ((star.v != null) && Array.isArray(star.v)) {
        star.u = star.v[0];
        star.w = star.v[2];
        star.v = star.v[1];  // Last to avoid premature overwrite
    }
    if (star.v == null) {
        star.u = 0;
        star.v = 0;
        star.w = 0;
    }

    star.size = getSize(star.spt);
    star.color  = getColor(star.spt);
        
    if (star.np > 0) {
        star.ring = {};
        var clr = 0x8800CC;
        if (star.np == 2) {
            clr = 0x00AAFF
        } else if (star.np > 2) {
            clr  = 0x00ff00;
        }
        star.ring.color = clr;
    }
    
};

export default PrepStar;

