import {Component} from "react";
import Util from "../std/Util.js";

class Units extends Component {
    
    constructor(args) {
        super(args);
        this.units = args.units;
        this.name  = args.name;
        this.row   = args.row;
    }
    
    render() {
        if (this.units == null || this.units.length == 0) {
            return <span></span>;
        }
        const elems = [];
        const here = this;
        elems.push(
          <button 
            key="0" 
            onClick={here.row.cycleUnits.bind(here.row)}
            hidden={!this.props.value}>
               &gt;&gt;
          </button>
        );
        elems.push(<span key="-1">&nbsp;&nbsp;</span>)
        for (var i=0; i<this.units.length; i += 1) {
            const unit = this.units[i];
            if (unit.unit == 'd' || (unit.scale == 1  && unit.unit != 'rad')) {
                elems.push(<span key={i+1}><strong>{unit.unit} </strong></span>);
            } else {
                elems.push(<span key={i+1}>{unit.unit} </span>);
            }
        }
        
        return <span>{elems}</span>;
    }
}

export default Units;

