import {Component} from "react";
import Util from "../std/Util.js";

class TextInput extends Component {
    constructor(props) {
        super(props);
        if (props.placeholder) {
            this.placeholder = props.placeholder;            
        } else {
            this.placeholder = "Input...";
        }
        if (props.callback) {
            this.callback = props.callback;
        } else {
            this.callback = null;
        }
        if (props.id) {
            this.localId = props.id;
        }
        this.state = {value: props.dft};
    }

    render() {
        let here = this;
        return (
            <input size="16" id={this.localId} placeholder={this.placeholder} defaultValue={this.state.value}
                onKeyUp={
                  (event) => {
                    if (event.key === 'Enter'  && here.callback) {
                        here.callback(event.target.value);                       
                    }
                  }
                }
            />
        )
    }
}

export default TextInput;