import Util from "../std/Util.js";
import ObjRow from "../elems/ObjRow.js";

import Dump from "../std/Dump.js";

import {Component} from "react";

class ObjList extends Component {
    
    constructor(props) {
        super(props);
        this.anim   = props.anim;
        this.list   = this.filterList(props.list);
        this.holder = props.holder;
        this.state = {keyID: ObjList.keyCount};
        ObjList.keyCount += 1;
    }
    static keyCount = 0;
    
    render() {
        if (this.list.length > 0) {
            let names = [];
            let max = this.list.length;
            if (max > 5) {
                max = 5;
            }

            let rows = [];
            for (let i=0; i<max; i += 1) {
                rows.push(<ObjRow key={i} anim={this.anim} holder={this.holder} entry={this.list[i]} /> );
            }
        
            let suffix = "";
            if (max < this.list.length) {
                let es = "";
                if (this.list.length > max+1) {
                    es = "es";
                }
                suffix = <span> and {this.list.length-max} other match{es}</span>;
            }

            return (
                    <div key={this.state.keyID}><ul>{rows}</ul>{suffix}</div>
            );
        } else {
            return <div key={this.state.keyID}> No match found </div>;
        }
    }
    
    filterList(list) {
        let list2 = [];
        let have = {};
        for (let i=0; i<list.length; i += 1) {
            
            let type = list[i].meta.type;
            let name = list[i].meta.name;
            let concat = name+type;
            // Don't show duplicates.
            if (have[concat]) {
                continue;
            } else {
                have[concat] = true;
            }
            // Filter out 'hidden' elements that aren't really shown.
            if ( type != 'hidden') { 
                list2.push(list[i]);
            }
        }
        list2.sort(function(a,b) {
            let an = a.meta.name;
            let bn = b.meta.name;
            if (a.component && b.component) {
                if (an == bn) {
                    return 0;
                } else if (a.name < b.name) {
                    return -1;
                } else {
                    return 1;
                }
            }
            if (a.component) {
                return -1;
            }
            if (b.component) {
                return 1;
            }
            if (a.distance || b.distance) {
                return a.distance - b.distance;
            } else {
                if (an < bn) {
                    return -1;
                } else if (an > bn) {
                    return 1;
                } else {
                    return 0;
                }
            }
        });
        return list2;
    }
    
}
export default ObjList;