import {Component} from "react";
import Units from "./Units.js";
import status from "./Status.js";

import ParameterFactory from "../orbits/ParameterFactory.js";
import Parameter from "../orbits/Parameter.js";

import Util from "../std/Util.js";

class Row extends Component {
    constructor(args) {
        super(args);
        this.param = args.param;
        this.calc  = args.calc;
        this.validated = false;
        this.unitIndex = -1;
        this.parameter =  null;
        this.showingUnit = false;
        this.state = {value: '', computed: false};
        this.values = null;
    }
    
    // This is called every time the value changes.
    handleChange(event) {
        this.validated = false;
        this.setState({value: event.target.value});
    }
    
    // This is only called when the user enters a return.
    validate(val) {
        var initVal = val;
        // Check if this is just a unit change.
        if (this.unitIndex >= 0  && this.values) {
            if (val == this.values[this.unitIndex]) {
                this.validated = true;
                return;
            }
        }
        this.parameter = null;
        this.unitIndex = -1;
        if (this.state.computed) {
            this.setState({computed: false});
        }
        this.values = null;
        if (val == null || val.length == 0) {
            this.validated = false;
            return;
        }
        
        val = val.trim();
        
        // Allow commas as separators.
        // Get rid of [] that may have been displayed.
        val = val.replace(/,/g, ' ');
        val = val.replace('[', ' ');
        val = val.replace(']', ' ');

        // Replace multiple spaces (or tab or ...) with a single space.
        val = val.replace(/\s+/g, " ");
        var elems = val.split(" ");
        var dim = null;
        if (elems.length === 2 || elems.length === 4 || elems.length === 10) {
            dim = elems.pop().toLowerCase();
        }
        var unit = null;
        if (dim && this.param.units) {            
            this.unitIndex = -1;
            var i;
            for (i = 0; i < this.param.units.length; i += 1) {
                const xunit = this.param.units[i];
                if (xunit.unit.toLowerCase() === dim) {
                    unit = this.param.units[i];
                    this.unitIndex = i;
                    break;
                }
            }
            
            if (this.unitIndex < 0) {
                status("Error: Unknown unit:"+dim+" for parameter "+this.param.name+". Last token ignored.");
                return;
            }
        }
        if (this.unitIndex < 0 && this.param.units) {
            this.unitIndex = this.getDftUnit();
            unit = this.param.units[this.unitIndex];
        }
        
        var scale = 1;
        var offset = 0;
        if (unit) {
            scale = unit.scale;
            if (unit.offset) {
                offset = unit.offset;
            }
        }
        var p;
        if (elems.length === 1) {
            if (this.param.dim !== Parameter.SCALAR) {
                status("Scalar specification for non-scalar parameter");
                return;
            }
            var val = elems[0];
            if (isNaN(val)) {
                status("Not a numerical value");
                return;
            }
            val = (Number.parseFloat(val)-offset)*scale;
            this.parameter = ParameterFactory.genParam(this.param.name, 
                               val, unit);

        } else if (elems.length === 3) {
            if (this.param.dim !== Parameter.VECTOR) {
                status("Vector specification for non-vector");
                return;
            }
            if (isNaN(elems[0])  || isNaN(elems[1] || isNaN(elems[2]))) {
                status("Not a numeric vector");
                return;
            }
            this.parameter = ParameterFactory.genParam(this.param.name,
                    [Number.parseFloat(elems[0]) * scale,
                     Number.parseFloat(elems[1]) * scale,
                     Number.parseFloat(elems[2]) * scale], unit);
            
        } else {
            status("Unexpected number of tokens in parameter:" + elems.length);
            return;
        }
        // Now generate the values for all of the units.
        if (unit) {
            // Pass the input value so that it can be used for the current
            // unit
            this.genValues(initVal);
        }
        this.validated = true;
        status("");
    }
    
    genValues(input) {
        if (!this.param.units) {
            return;
        }
        const units = this.param.units;
        this.values = [];
        for (var i=0; i<units.length; i += 1) {
            if (i == this.unitIndex) {
                this.values[i] = input;
            } else {
                this.values[i] = this.renderToUnit(units[i]);
            }
        }
    } 
    
    getDftUnit() {
        for (var i=0; i<this.param.units.length; i += 1) {
            const unit = this.param.units[i];
            if (unit.unit == "d" || (unit.scale == 1  && unit.unit != 'rad')) {
                return i;
            }
        }
        Util.log("No default unit found:"+this.param.name);
    }
    
    cycleUnits() {
        if (!this.validated) {
            this.validate(this.state.value);
        }
        if (!this.validated) {
            status("Invalid entry, cannot cycle units");
            return;
        }        
        this.unitIndex = (this.unitIndex + 1)%this.param.units.length;
        this.setState({value: this.values[this.unitIndex]});
    }
    
    renderNow() {
        return this.valuesArr[this.unitIndex] + this.param.units[this.unitIndex].unit;
    }
    
    renderToUnit(unit) {
        if (!this.parameter) {
            return "";
        }
        const scale  = unit.scale;
        const offset = unit.offset? unit.offset: 0;
        
        if (this.param.dim == Parameter.SCALAR) {
            return this.dispScalar(this.parameter.getScalar(), 7, scale, offset)+" "+unit.unit;
        } else {
            const vec = this.parameter.getVector();
            return "[ "+this.dispVector(vec, 5, scale, offset)+" ] "+unit.unit;
        }
    }
    
    dispScalar(val, prec, scale, offset) {
        return (val/scale+offset).toPrecision(prec);
    }
    
    doubler() {
        this.calc.tryRowUpdate(this);
    }
    
    dispVector(vec, prec, scale, offset) {
        var str = "";
        var prefix = "";
        for (var i=0; i<vec.length; i += 1) {
            str += prefix + this.dispScalar(vec[i], prec, scale, offset);
            prefix = " ";
        }
        return str;
    }
    
    render() {
        const param = this.param;
        const here = this;
        const cname = this.state.computed? "yellow": "white";
        let sep = "";
        if (param.name == "m1" && this.state.value) {
            sep = <select id="frame"><option>CoM</option><option>Sep</option></select>;
        }
        return (
                <tr key={param.name} className={cname}>
                    <th align="right">{param.name}</th>
                    <td><input size="18"
                          value={this.state.value}
                          onChange={this.handleChange.bind(this)}
                          onDoubleClick={this.doubler.bind(this)}
                          onKeyUp={
                            (event) => {
                              if (event.key === 'Enter') {
                                  here.validate(event.target.value);                       
                              }
                             }
                           }/></td>
                    <td><Units row={this} value={this.state.value} name={param.name} units={param.units} /></td>
                    <td>{sep}{param.descrip}</td>
                </tr>
        );
    }
    
    setParameter(p) {
        this.unitIndex = -1;
        this.parameter = p;
        this.validated = true;
        if (this.param.units) {
            
            this.genValues("");
            this.unitIndex = this.getDftUnit() - 1;  // Might be -1, but that's ok.
            this.cycleUnits();
            this.setState({computed: true});
        } else {
            // No units
            var dispVal;
            if (this.param.dim == Parameter.SCALAR) {
                dispVal = this.dispScalar(p.getScalar(), 7, 1,0);
            } else if (this.param.dim == Parameter.VECTOR) {
                dispVal = this.dispVector(p.getVector(), 5,1,0);
            } else if (this.param.dim == Parameter.BLOB) {
                dispVal = this.dispVector(p.getVector(), 4, 1, 0);
            }
            this.setState({value: dispVal, computed: true});
        }
    }
}

export default Row;
