// Calculate the precession rates for the longitude
// of the ascending node and the argument of perigee
// for Earth satellites.  Source:
// https://science.nasa.gov/science-pink/s3fs-public/atoms/files/GDC_OrbitPrimer.pdf

import Constants from "../std/Constants";
class SatPrecession {
    
    constructor(orbit) {
        
        let a  = orbit.a;
        let rE = Constants.rEarth;
        let mE = Constants.MEarth;
        let G  = Constants.G;
        let e  = orbit.e;
        let e2 = e*e;
        let cos_i = orbit.rot[3]/orbit.rot[0];
        let sin_i = orbit.rot[4]
        
        // This term depends upon the shape of the Earth.
        let j2 = 1.08262668e-3; //https://ai-solutions.com/_freeflyeruniversityguide/j2_perturbation.htm
        
        // if p = 3 sqrt(GM/a^3) J2 rE^2 / [2 a^2 (1-e^2)^2)
        //    nodeP = -p*cos(i)
        //    argP  =  p*(4-5sin(i)^2)/2  
        
        let p = 3*Math.sqrt(G * mE/(a*a*a)) * j2 * rE*rE / (2*a*a*(1-e2)*(1-e2));
        this.fOmega    = -p*cos_i;
        this.fNu =  p*(4-5*sin_i*sin_i)/2;
    }
}

export default SatPrecession;