import Util             from "../std/Util";
import Orbit            from "../orbits/Orbit";
import ParameterFactory from "../orbits/ParameterFactory";
import Constants        from "../std/Constants";
import SatPrecession    from "../earth/SatPrecession";

function NoradReader(url, callback) {
    const xhr = new XMLHttpRequest();
    const here = this;
    const xhrHandler = function() {
        
        if (xhr.readyState === XMLHttpRequest.DONE) {
            const data = parseNorad(xhr.responseText);
        };
    }
    xhr.open('GET', url);
    xhr.onreadystatechange = xhrHandler;
    xhr.send();
    
    var latestEpoch = -1e30;
    var smallestA   = 1.e10;
    
    function parseNorad(text) {
        var lines = text.split("\n");
        var start = 0;
        const data = [];
        while (start < lines.length) {
            if (lines[start].charAt(0) == "0") {
                const res = parseLines(lines[start], lines[start+1], lines[start+2]);
                if (res) {
                    let satp = new SatPrecession(res.orbit);
                    res.orbit.addPrecession(res.orbit.ep0, satp.fOmega, satp.fNu);
                    data[data.length] = res;
                }
                start += 3;                
            } else {
                if (lines[start].trim().length > 0) {
                    Util.log("Unexpected line in tle:\n  ",lines[start]);
                }
                // Need to have 0 line to start.
                start += 1;
            }
        }
        if (callback) {
            callback(latestEpoch, data);
        }
        
    }
    
    var count = 0;
    function parseLines(l0,l1,l2) {
        count += 1;
        if (!l1.charAt(0) == "1"  || !l2.charAt(0) == "2") {
            Util.log("Invalid TLE detected:\n  ", l0,"\n  ", l1,"\n  ", l2 );            
            return null;
        }
        var name  = l0.substring(2).trim();
        if (name.startsWith("TBA ")) {
            name="TBA";
        }
        var id    = l1.substring(2,7).trim();
        var desig = l1.substring(9,17).trim();
        var yr = l1.substring(18,20);
        if (yr.charAt(0) > '4') {
            yr = "19" +yr;            
        } else {
            yr = "20"+yr;
        }
        yr = parseInt(yr);
        var yday = l1.substring(20,32).trim();
        yday = parseFloat(yday);
        
        var inclination = l2.substring(8,16).trim();
        var ascNode     = l2.substring(17,25).trim();
        var ecc         = "0."+l2.substring(26,33).trim();
        var argPerigee  = l2.substring(34,42).trim();
        var ma          = l2.substring(43,51).trim();
        var mm          = l2.substring(52,63).trim();
        var idays       = (yr-2000) * 365;  // Completedyears
        // Leap days in the current year are handled in the yday
        // but we need to add them for previous years.  (Just handling since 2000)
        // Recall that 2000 is a leap year.  Note that 2004 has only
        // one special leapday, since if we are past 2/28 this is included
        // in yday.
        var leap = 0;
        if (yr > 2000) {
           leap = Math.floor( (yr-2001)/4) + 1;  // 2001 -> 1, 2004 -> 1, 2005 -> 2           
        }
        var days  = idays + leap + yday;
        var t = Constants.SOLAR_DAY * days;
        
        if (t > latestEpoch  || count < 3) {
            latestEpoch = t;
        }
        
        var orbit = new Orbit(name);
        const pgen = ParameterFactory.genParam;
        orbit.addParameter(pgen("M",     Constants.MEarth));
        orbit.addParameter(pgen("t",     t));
        orbit.addParameter(pgen("N0",    mm));
        orbit.addParameter(pgen("i",     inclination*Math.PI/180));
        orbit.addParameter(pgen("nu",    argPerigee*Math.PI/180));
        orbit.addParameter(pgen("Omega", ascNode*Math.PI/180));
        orbit.addParameter(pgen("MA",    ma*Math.PI/180));
        orbit.addParameter(pgen("Eccen", ecc));
        
        var rot = orbit.getParameter("Rot", true);
        var a   = orbit.getParameter("a", true);
        var t0  = orbit.getParameter("t0", true);
        var per = orbit.getParameter("per", true);
        var e   = orbit.getParameter("Eccen", true);
        if (a.value < smallestA) {
            smallestA = a.value;
        }
        var qorb = orbit.quickOrbit();
        qorb.ep0 = t;
        qorb.scale = 1./Constants.rEarth;
        return {name: name+"_"+desig+"_"+id, id: id, orbit: qorb};
    }
}

export default NoradReader;


