
class Constants {
    
    //// Physics and Astronomy
    
    /** Product of  G and MSun which is known better than either individually. */
    static GMSun = 1.32712440018e20;   // [m3/s2] 
    
    /** Gravitational constant. */
    static G     = 6.6743E-11;         // [m3/kg/s2]
    
    /** The mass of the sun */
    static MSun  = Constants.GMSun/Constants.G;            // [kg] 1.989E30;
    
    /** Mass of the earth */
    static MEarth = 1/332946.0487 * Constants.MSun;
    
    
    
    
    /** The length of an astronomical unit */
    static AU    = 149597870700.;      // [m]
    
    /** Derive length of parsec */
    static PC    = Constants.AU * 3600 * 180/Math.PI;
    
    /** Length of century...  assuming 24.25 leap days/century */
    static CENTURY = (365*100 + 24.25)*86400; // [s] 
    
    
    /** Julian day of 2000-01-01.0 */
    static JD0 = 2451544.5;                   // [days]
    
    /** Unix epoch of year,month,day  */
    static UTCDate(year, month, day) {
        let date = new Date();
        date.setUTCFullYear(year);
        date.setUTCMonth(month-1);  // Remember difference.
        date.setUTCDate(day);
        date.setUTCHours(0);
        date.setUTCMinutes(0);
        date.setUTCSeconds(0);
        date.setUTCMilliseconds(0);
        return date;
    }
    
    static MJD(time) {
        return time/86400 + Constants.JD0;
    }
    
    static MJDtime(mjd) {
        return 86400 * (mjd-Constants.JD0);
    }
    
    static Epoch0 = Constants.UTCDate(2000, 1, 1).getTime()/1000;   // [s] 
    
    /** Diameter of the Sun */
    static dSun = 1.392e9;  // m 
    
    /** Radius of the Earth */
    static rEarth = 6.3781e6; // m
    
    static SOLAR_DAY    = 86400;      // s
    static SIDEREAL_DAY = 86164.0905; // s 
    
    // Angle of Earth's rotation at 2000.0
    static SIDEREAL_ANGLE_AT_EPOCH = 175.1731379442;   // d (from http://neoprogrammics.com/sidereal_time_calculator/index.php
 
    //// Programmatic constants
    
    /** Criterion for planar orbit (to prefer periapsis vector to line of nodes) */
    static PLANAR_RATIO = 1.e-10;
    
    /** Criterion for radial (1-e) or circular (e) orbit. */
    static SHAPE_RATIO = 1.e-10;
        
    /** Delta required to halt Newton-Raphson.  Note that you
     *  can just use the values for angles in radians without scaling.
     */
    static  HALT_RATIO = 1.e-12;
    
    
    // Units
    static radUnits = [{unit: "au", scale: Constants.AU}, {unit: "km", scale: 1000}, {unit: "m", scale: 1}, 
                  {unit: "rE", scale: Constants.rEarth}];

    static angUnits =  [{unit: "d", scale: Math.PI/180}, {unit: "rad", scale:1}];

    static timeUnits = [{unit: "day",    scale: 86400}, {unit: "s", scale:1}, 
                   {unit:"jd", scale: 86400, offset: Constants.JD0}];

    static velUnits = [{unit: "au/day", scale: Constants.AU/86400}, 
                       {unit: "km/s", scale: 1000}, {unit: "m/s", scale: 1}];
                   
    static massUnits = [{unit: "Ms", scale: Constants.MSun}, {unit: "Me", scale: Constants.MEarth},
        {unit:"kg", scale: 1}];
    
    static intervUnits = [{unit: "day", scale: 86400}, {unit: "y", scale: 31558149.76}, 
                         {unit: "s", scale: 1}];

}

export default Constants;