import LocalGalaxy     from "./LocalGalaxy.js";
import SolarSystem     from "./SolarSystem.js";
import PlanetarySystem from "./PlanetarySystem.js";
import Galaxies        from "./Galaxies.js";
import ssData          from "../data/PlanetaryData.js";

class AnimationFactory {
  
    constructor(){
        this.cache = {};
        this.available = {};
        for (let planet in ssData.Sol.planets) {
            this.available[planet.toLowerCase()]      = 1;
        }
        this.available["solar system"]                = 1;
        this.available["local galactic neighborhood"] = 1;
        this.available["the earth and environs"]      = 1;
    };
    
    getAnimation(name, args) {
        name = name.toLowerCase();
        if (!this.cache[name]) {
            switch (name) {
              case "mercury":
              case "venus":
              case "mars":
              case "jupiter":
              case "saturn":
              case "uranus":
              case "neptune":
              case "pluto":
                let capName = name.substring(0,1).toUpperCase()+name.substring(1);
                this.cache[name] = new PlanetarySystem(capName, args);
                break;
                
                
              case "earth":
              case "the earth and environs":
                this.cache[name] = new PlanetarySystem("Earth", args);
                break;
                               
              case "solar system":                    
                this.cache[name] = new SolarSystem(args);
                break;
                
              case "local galactic neighborhood":
                this.cache[name] = new LocalGalaxy(args);
                break;
                
              case "nearby galaxies":
                 this.cache[name] = new Galaxies(args);
                 break;                
            }
            
        }
        return this.cache[name];
    }
    
    isAnimation(name) {
        return this.available[name.toLowerCase()];
    }
}
const singleton = new AnimationFactory();

// Export the instance not the class.
export default singleton;