import {SphereGeometry, MeshBasicMaterial, Mesh} from "three";
import Util from "../std/Util.js";
import Constants from "../std/Constants.js";

const Planet = function(name, start, qorbit, size, color, wireFrame, useSize){
    
 
    // We're going to make things smaller than the Sun, but 
    // can't scale directly or they will disappear...
    
    var ratio = (Math.log10(size/10000)+1)*.03;
    if (useSize) {
        ratio = size;
    }
    
    const geometry = new SphereGeometry(ratio, 8, 8);
    const material = new MeshBasicMaterial({wireframe: wireFrame, color: color });
    const sphere   = new Mesh(geometry, material);
    const p        = qorbit.position(start);
    sphere.position.set(p[0], p[1], p[2]);
    let count = 0;
        
    const ret = 
      {component: sphere, name: name+"Body",
        
      render: function(time) {
          // Update planet positions.
           var r = qorbit.position(time);
           // Using ret.component vs just sphere
           // allows us to replace the component externally.
           ret.component.position.set(r[0], r[1], r[2]);
      }};
    return ret;
};

export default Planet;

