/** This file contains the generators for all of the orbital parameters that
 *  can be determined from other parameters.
 *  
 *  We use the Parameter.addGenerator method to add each generator to
 *  the parameter factory. This method has three arguments that define
 *  each generator:
 *     - A string giving the standard name of the parameter which can be
 *       inferred from known parameters. ("a" in the first generator)
 *     - An array of strings giving the standard names of the parameters that
 *       are needed to infer the new parameter.  Default values for constants
 *       can be inferred using an 0 length array. ("G", "M", "E" in the first generator)
 *     - A function which describes how the new parameter can be determined from
 *       the existing parameters.  It takes one argument: a hash of the known
 *       parameters, called params here.  E.g., in the first generator we calculate
 *       the semimajor axis using a = -.5GM/E (recalling that the energy for a bound
 *       orbit is negative).
 *       
 *  The may be multiple generators for a given output parameter, and these 
 *  will be checked in order specified to see if the prerequisite parameters
 *  are known.  
 *  
 *  In a few special situations, even when all of the prerequisite parameters
 *  are known, the parameter cannot be determined, so that the function returns a null.
 *  
 *  The ParameterFactory.genParameter function adds the type and and varies information
 *  to the value of the parameter that we determine here. 
 *  
 */

import Constants        from "../std/Constants.js";
import Util             from "../std/Util.js";
import ParameterFactory from "./ParameterFactory.js";
import Kepler           from "./Kepler.js";

const LoadOrigins = function () {
    // We enclose all of this in a function to enable localization internal
    // variables and functions.
    // 
    // Shortcut names.
    const gadd = ParameterFactory.addGenerator;
    const pgen = ParameterFactory.genParam;
	
	if (ParameterFactory.generators != null) {
		return;
	}
	
	ParameterFactory.generators = {};

    /** a: the semimajor axis of the orbit.  This is undefined for
     *  parabolas, and negative for hyperbolas.
     */

    gadd("a", ["G", "M", "E"],
            function (params) {
                // a = GM/2E
                const G = params["G"].value;
                const M = params["M"].value;
                const E = params["E"].value;
                if (E === 0) {
                    Util.log("Parabolic orbit, a is undefined from E");
                    return pgen("a", Math.NaN);
                }
                const val = -0.5 * G * M / E;
        
                return pgen("a", val);
            }
    );
    
    gadd("a", ["G", "M", "m1", "Per"],
      // Kepler's law
      // a = cbrt(g(M+m1)p^2 /  4pi^2)
      function(params) {
          const g = params["G"].value;
          const p = params["Per"].value;
          const m = params["M"].value;
          const dm = params["m1"].value;
          const a = Math.cbrt(g*(m+dm)*p*p/(4*Math.PI*Math.PI));
          return pgen("a", a);
      }
    );

    /** b: the semiminor axis */
    gadd("b", ["Ecc", "a"],
            function (params) {
                const e = Util.mag(params["Ecc"].value);
                const a = params["a"].value;
                var sign = 1;
                if (e > 1) {
                    sign = -1;
                }
                
                return pgen("b", a * Math.sqrt(sign*(1 - e * e)));
            }
    );

    /** C: The center of the ellipse relative to the focus of rotation. */
    gadd("C", ["a", "Ecc"],
            function (params) {

                // The center of the eccentricity vector is pointing
                // To the periapsis and has a magnitude of the eccentricity.
                // The distance between the focus and the center is just the
                // eccentricty x the semimajor axis.
                const e = params["Ecc"].value;
                const em = Util.mag(e);

                const a = params["a"].value;

                if (em < 1.e-12) {
                    return pgen("C", [0, 0, 0]);

                } else if (Math.abs(em - 1) < 1.e-12) {
                    Util.log("Center not defined for parabola");
                    return null;

                } else {
                    // In the opposite direction from the periapsis
                    // reduced from a by eccentricity, which is
                    // conveniently already in the eccentricity vector.
                    // Not that for hyperbolic orbits a is negative, and
                    // the other focus is further out than the periapsis point.
                    const c = Util.times(-a, e);
                    return pgen("C", c);
                }
            }
    );

    /**
     * E: The energy of the orbit.
     */
    gadd("E", ["G", "M", "r", "v"],
            function (params) {

                const v = params["v"].getScalar();
                const r = params["r"].getScalar();
                const g = params["G"].value;
                const m = params["M"].value;
                return pgen("E", v * v / 2 - g * m / r);
            }
    );

    gadd("E", ["G", "M", "a"],
            function (params) {
                const g = params["G"].value;
                const m = params["M"].value;
                const a = params["a"].value;
                return pgen("E", -0.5 * g * m / a);
            }
    );

    /**
     * EA: The eccentric anomaly.
     * The angle between the periapsis and a projection of the particle onto the
     * circle in which the ellipse is embedded,
     * using a projection perpendicular to the vector to periapsis.
     */
    gadd("EA", ["TA", "Ecc"],
            function (params) {
                const em = params["Ecc"].getScalar();
                const ta = params["TA"].value;

                // tan (ta/2) = sqrt((1+e)/(1-e)) * tan (ea/2)
                var sign = 1;
                if (em > 1) {
                    sign = -1;
                }

                var ea = Math.sqrt(sign*(1 - em) / (1 + em)) * Math.tan(ta / 2);
                if (em < 1) {
                    ea = 2*Math.atan(ea);
                    // For periodic orbits make range 0 to 2PI
                    if (ta > Math.PI && ea < Math.PI) {
                        ea = 2 * Math.PI - ea;
                    }
                } else {
                    // For unbound we want will leave range centered on 0.
                    ea = 2*Math.atanh(ea);
                }



                return pgen("EA", ea);
            }
    );
    

    gadd("EA", ["MA", "Ecc"],
            function (params) {
                
                const ma  = params["MA"].value;
                const ecc = params["Ecc"].getScalar();
                const ea  = Kepler(ma, ecc);
                if (ea == null) {
                    return null;
                } else {                    
                    return pgen("EA", ea);
                }
            }
    );

    /**
     * Ecc: The eccentricity vector of the ellipse which points to the periapsis and
     * has a magnitude equal to the eccentricity.
     */
    gadd("Ecc", ["G", "M", "r", "v"],
            function (params) {
                const r = params["r"].value;
                const v = params["v"].value;
                const rm = Util.mag(r);
                const G = params["G"].value;
                const M = params["M"].value;
                const f1 = Util.times(1/(G*M), Util.cross(v, Util.cross(r,v)));
                const f2 = Util.times(-1/rm, r) 
                return pgen("Ecc", Util.vadd(f1,f2));
            }
    );

    gadd("Ecc", ["Eccen", "Rot", "nu"],
            function (params) {
                const e = params["Eccen"].getScalar();
                const rot = params["Rot"].value;
                const nu  = rot[5];
                
                // We want the point where TA is 0.
                
                const cos = e*Math.cos(nu);
                const sin = e*Math.sin(nu);
                
                const vec = [rot[0]*cos + rot[1]*sin,
                             rot[2]*cos + rot[3]*sin,
                             rot[4]*sin];
                         
                return pgen("Ecc", vec);
            }
    );

    /** The scalar eccentricity is not generally used internally
     *  but is often supplied as input.  We include it here for completeness.
     */
    gadd("Eccen", ["Ecc"],
            function (params) {
                return pgen("Eccen", params["Ecc"].getScalar());
            }
    );


    /**
     * eF2: The energy scaled vector to the second focus.
     * Note that this goes to invinity only as the energy of the orbit goes to infinity.
     */
    gadd("eF2", ["Ecc", "E", "a"],
            function (params) {

                // The second focus is 2ea from the primary focus
                // in the opposite direction of the Ecc vector.
                const eccv = params["Ecc"].value;
                const e = params["E"].value;
                const a = params["a"].value;

                const ef2 = Util.times(-2 * a * e, eccv);
                return pgen("eF2", ef2);
            }
    );

    /** G: Gravitational constant.  MKS by default. */
    gadd("G", {},
            function (params) {
                return pgen("G", Constants.G);
            }
    );

    /**
     * i: The inclination of the orbital plane relative to the equatorial plane of the coordinate system
     *    (the ecliptic for solar orbits).  For non-radial orbits we
     * can get this from the angular momentum vector but for radial orbits
     * we need to use the position or velocity vectors.
     */
    gadd("i", ["L"],
            function (params) {
                const L = params["L"].value;
                const mag = Util.mag(L);
                if (mag === 0) {
                    Util.log("Radial orbit so computation of inclination from angular momentum fails.");
                    return null;
                } else {
                    const dot = Util.dot(L, [0, 0, 1]) / Util.mag(L);
                    return pgen("i", Math.acos(dot));
                }
            }
    );

    // From r,v.  This works for radial orbits too.
    gadd("i", ["r", "v"],
            function (params) {
                var rv = params["r"].value;
                var rm = Util.mag(rv);
                const vv = params["v"].value;
                const vm = Util.mag(vv);

                const L = Util.cross(rv, vv);
                const Lm = Util.mag(L);

                // Check for redial orbit...
                if (Lm > Constants.SHAPE_RATIO * rm * vm) {
                    const dot = Util.dot(L, [0, 0, 1]) / Util.mag(L);
                    return pgen("i", Math.acos(dot));
                } else {
                    // A radial orbit doesn't have a direction of revolution,
                    // so we will always pick a positive inclination.
                    if (rm === 0) {  // Substitute the velocity vector if we are at the origin.
                        rv = vv;
                        rm = vm;
                    }
                    if (rm === 0) {
                        Util.log("Inclination undefined: Singular orbit, stationary at origin");
                        return null;
                    }
                    var dot = Util.dot(rv, [0, 0, 1]) / rm;
                    dot = Math.abs(dot);
                    return pgen("i", Math.PI / 2 - Math.acos(dot));
                }
            }
    );

    /** L: The angular momentum vector */
    gadd("L", ["r", "v"],
    
            function (params) {
                const r = params["r"].value;
                const v = params["v"].value;
                const l = Util.cross(r, v);

                return pgen("L", l);
            }
    );

    gadd("L", ["G", "M", "Eccen", "E", "Rot"],
            function (params) {
                const E   = params["E"].value;
                const rot = params["Rot"].value;
                
                // Note that this is the eccentricity scalar, not vector
                
                const ecc = params["Eccen"].value;
                const g   = params["G"].value;
                const m   = params["M"].value;

                // Get the magnitude of the angular momentum.
                const Lm = g * m * Math.sqrt((ecc * ecc - 1) /(2 * E));
                                // Six element vector with to make efficient position computations.
//                rot       = [0:co,  1:-so*ci,  // Coefficients for x 
//                             2:so,  3:co*ci,   // Coefficients for y
//                             4:si,             // Coefficients for z
//                             5:n]              // Save nu so all rotation information stored in rot.    

                
                // L = Lm [so si, -co si, co]
                // Rot is intended to rotate the x axis, so we have to play
                // a little to rotate the z axis.
                
                var l = [Lm*rot[2]*rot[4], -Lm*rot[0]*rot[4], Lm*rot[3]/rot[0] ];

                return pgen("L", l);
            }
    );

    /**
     * LonP: Longitude of Perihelion.
     * This seems to be just the argument of perihelion+the longitude of the ascending node.
     */
    gadd("LonP", ["Omega", "nu"],
            function (params) {
                const nu = params["nu"].value;
                const omega = params["Omega"].value;

                return pgen("LonP", nu + omega);
            }
    );
    

    /** M: Mass of the central object: defaults to mass of the Sun */
    gadd("M", [],
            function (params) {
                return pgen("M", Constants.MSun);
            }
    );
    
    /** m1: The mass of the orbiting body.  Defaults to 0 */
    gadd("m1", [],
        function(params) {
            return pgen("m1", 0);
        }
    );
    

    /** MA: The mean anomaly is a uniform measure of time as phase along the orbit,
     *  E.g., as in our first definition: just 2*PI*(t-t0)/period where t0 is the time of periapsis 
     **/
    gadd("MA", ["t", "t0", "Per"],
            function (params) {
                // The Mean anomaly is defined as
                // MA = 2*Pi*(t-t0)/Period.
                const p = params["Per"].value;
                if (p === Number.POSITIVE_INFINITY) {
                    return null;
                }
                
                const t = params["t"].value;
                const t0 = params["t0"].value;

                var ma = (t - t0) / p;
                ma = ma - Math.floor(ma);
                ma *= 2 * Math.PI;

                return pgen("MA", ma);
            }
    );
    

    gadd("MA", ["EA", "Ecc"],
            function (params) {
                const e = params["Ecc"].getScalar();
                const ea = params["EA"].value;
                if (e < Constants.SHAPE_RATIO) {
                    // If the orbit is circular then MA = EA
                    return pgen("MA", ea);
                }

                var ma;
                // Kepler's equation in the easy direction
                if (e < 1) {                    
                    ma = ea - e * Math.sin(ea);
                } else {
                    ma = e*Math.sinh(ea) - ea;
                }
                return pgen("MA", ma);
            }
    );
    
    // This definition might get used for hyperbolic orbits.
    gadd("MA", ["t", "t0", "G", "M", "a"],
            function(params) {
                 const t   = params["t"].value;
                 const t0  = params["t0"].value;
                 const G   = params["G"].value;
                 const M   = params["M"].value;
                 // For hyperbolic orbits a < 0.
                 const a   = Math.abs(params["a"].value);
                 const ma = Math.sqrt(G*M/(a*a*a)) * (t-t0);
                 return pgen("MA", ma);
             }
    );

    gadd("MA", ["MLon", "Omega", "nu"],
            function (params) {
                const ml = params["MLon"].value;
                const omega = params["Omega"].value;
                const nu = params["nu"].value;
                return pgen("MA", ml - omega - nu);
            }
    );

    /**
     * MLon: Mean longitude
     * This seems to be just the argument of perihelion+the longitude of the ascending node.
     */
    gadd("MLon", ["MA", "Omega", "nu"],
            function (params) {
                const ma    = params["MA"].value;
                const nu    = params["nu"].value;
                const omega = params["Omega"].value;

                return pgen("MLon", ma + nu + omega);
            }
    );
    
    /**
     * N0: Mean motion in orbits/day
     */
    gadd("N0", ["Per"],
        function(params) {
           return pgen("N0", 86400./params["Per"].value);
        }
    );

    /**
     * nu: Argument of periapsis.
     * This is the angle between the line of nodes and 
     * the vector to the periapsis.
     */
    gadd("nu", ["Ecc", "Pnode", "L"],
            function (params) {
                var p = params["Pnode"].value;
                var e = params["Ecc"].value;
                var L = params["L"].value;

                if ((Util.mag(p) === 0) || (Util.mag(e) === 0)) {
                    Util.log("Circular orbit or in plane so  argument of periapsis not defined. using 0");
                    return pgen("nu", 0);
                } else {
                    const cs = Util.dot(p, e) / Util.mag(p) / Util.mag(e);
                    var   nu = Math.acos(cs);
                    // Chech whether we are in 0-PI or PI-2PI
                    const cross = Util.cross(p, e);
                    const dir = Util.dot(cross, L);
                    if (dir < 0) {
                        nu = 2*Math.PI - nu;
                    }
                    return pgen("nu", nu);
                }
            }
    );

    gadd("nu", ["LonP", "Omega"],
            function (params) {
                const lp = params["LonP"].value;
                const omega = params["Omega"].value;

                return pgen("nu", lp - omega);
            }
    );

    /**
     * Omega: The longitude of the ascending node.  Normally the
     * longitude of the point at which the orbit crosses the equator going up.
     * This is ill defined for radial, circular or equatorial orbits.
     * If the orbit is in the equatorial plane, the longitude of the periapsis is used.
     * If the orbit is radial, then the direction of the orbit when it rising through the
     * plane is used.  If the orbit is radial in the z direction, or circular in the 
     * equatorial plane, the the value is set to 0.
     */

    gadd("Omega", ["Pnode"],
            function (params) {

                // Just the longitude of the Pnode vector.
                // Returned in radians.

                var pnode = params["Pnode"].value;
                pnode = Util.times(1 / Util.mag(pnode), pnode);
                const x = pnode[0];
                const y = pnode[1];
                var res = 0;
                if (Math.abs(x) > Constants.PLANAR_RATIO || Math.abs(y) >= Constants.PLANAR_RATIO) {
                    res = Math.atan2(y, x);
                    return pgen("Omega", res);
                } else {
                    // In the plane or radial
                    return null;
                }
            }
    );

    /** Per: The period of the orbit in seconds */
    gadd("Per", ["N0"],
        function(params) {
           return pgen("Per", 86400./params["N0"].value);
        }
    );


    gadd("Per", ["a", "b", "L"],
    
            // The area of the ellipse is pi a b 
            // The rate at which we are sweeping out area is 1/2 rv where we have
            // the 1/2 since we are sweeping out little triangles, not rectangles.
            function (params) {
                const a = params["a"].value;
                if (a <= 0) {
                    // Unbound orbit
                    return pgen("Per", Number.POSITIVE_INFINITY);
                }
                const b = params["b"].value;
                const L = params["L"].getScalar();
                // while 
                if (a * b === 0 || L === 0) {
                    Util.log("Cannot calculate period for radial orbit");
                    return null;
                } else {
                    return pgen("Per", 2*Math.PI * a * b / L);
                }
            }
    );
    
    gadd("Per", ["a", "G", "M", "m1"],
            function(params) {
                const a = params["a"].value;
                if (a <= 0) {
                    // Unbound orbit
                    return pgen("Per", Number.POSITIVE_INFINITY);
                }
                const G = params["G"].value;
                const M = params["M"].value;
                const m1 = params["m1"].value;
                return pgen("Per", 2*Math.PI/Math.sqrt(G*(M+m1)) * Math.pow(a, 1.5));
            }
    );
    

    /**
     * Pnode: A vector along the line of nodes.  As discussed with the parameter Omega there
     * are a number of special cases when the line of nodes is not well defined.
     * and various 'backup' definitions are used.  
     */
    gadd("Pnode", ["L"],
            function (params) {
                // p = z x L  where z is the z unit vector.

                const l = params["L"].value;
                const mag = Util.mag(l);
                const z = [0, 0, 1];
                const pnode = Util.cross(z, l);
                const pmag = Util.mag(pnode);
                // If the magnitude of the angular momentum perpendicular to
                // the z axis is too small we may have a circular orbit.
                if (pmag / mag < Constants.PLANAR_RATIO) {
                    return null;
                }
                // Make it a unit vector...
                return pgen("Pnode", Util.times(1/pmag, pnode));
            }
    );

    gadd("Pnode", ["L", "Ecc"],
            function (params) {

                // If this generator is called, then L is defined,
                // but the previous generator returned null.  This means that
                // The orbit is in the plane or radial.

                const ecc = params["Ecc"].value;
                const eccm = Util.mag(ecc);
                const x = ecc[0];
                const y = ecc[1];

                // If the orbit is cicular or along the z axis, the choice of
                // the vector to the node is arbitrary and we just have it point along
                // the x axis.
                if ((eccm < Constants.SHAPE_RATIO) ||
                        (Math.abs(x) < Constants.SHAPE_RATIO && Math.abs(y) < Constants.SHAPE_RATIO)) {
                    return pgen("Pnode", [1, 0, 0]);
                }
                return pgen("Pnode", Util.times(1 / eccm, ecc));

            }
    );

    /**
     * r: The instantaneous position vector of the object.
     * In this context we are deriving the position at a given time (using TA)
     * given the orbital elements of the system.  Presumably i and Omega were
     * used in computing Rot.
     */
    gadd("r", ["Rot", "Ecc", "a", "TA"],
            function (params) {
                
                const rot = params["Rot"].value;
                const e   = params["Ecc"].getScalar();
                const a   = params["a"].value;
                const ta  = params["TA"].value;
                
                
                // First we compute the position vector in the plane of the orbit with
                // x pointing to the periapsis.
                // The radius as a function of TA is                
                const  r = a * Math.abs(1 - e * e) / (1 + e * Math.cos(ta));
                // So the vector in the plane is just
                
                const nu  = rot[5];
                const cos = r*Math.cos(ta+nu);
                const sin = r*Math.sin(ta+nu);

                const rv = [rot[0]*cos + rot[1]*sin, 
                            rot[2]*cos + rot[3]*sin,
                            rot[4]*sin];
                        
                return pgen("r", rv);
            }
    );


    /**
     * Rot: information used in the rotation computation  
     *  Given the scalar radius rm, we get the components of r_i using
     *   r_i = r* ( rot[i][0]*cos(TA+nu) + rot[i][1]*sin(TA+nu)   ) 
     *   To get back standard angles:
     *       nu    = rot5
     *       Omega = atan2(rot2, rot0)
     *       i     = atan2(rot4, rot3/rot0)
     */
    gadd("Rot", ["Omega", "i", "nu"],
            function (params) {
                
                const o  = params["Omega"].value;
                const co = Math.cos(o);
                const so = Math.sin(o);
                
                const i  = params["i"].value;
                const ci = Math.cos(i);
                const si = Math.sin(i)
                
                const n  = params["nu"].value;
                
                // Six element vector with to make efficient position computations.
                const vec = [co,  -so*ci,  // Coefficients for x 
                             so,   co*ci,  // Coefficients for y
                             si,           // Coefficients for z
                             n]            // Save nu so all rotation information stored in rot.    
                return pgen("Rot", vec);
            }
    );
    
    /**
     * t: the current time.  Note that this is modulo the orbital period
     * for bound orbits.
     */
    gadd("t", ["t0", "MA", "Per"],
            function (params) {
                const per = params["Per"].value;
                if (per === Number.POSITIVE_INFINITY) {
                    return null;  // Hyperbolic orbit, need different info.
                }
                const t0 = params["t0"].value;
                const ma = params["MA"].value;
                const t = t0 - ma / (2 * Math.PI) * per;
                return pgen("t", t);
            }
    );
    gadd("t", ["t0", "MA", "Per", "a", "G", "M"],
            function (params) {
                // To get here, we must have had the period be infinite in
                // the previous generator, so we have a hyperbolic orbit.
                // Don't need the period, but it's a needed argument
                // to ensure that precondition is met.
                const t0 = params["t0"].value;
                const ma = params["MA"].value;
                const a = Math.abs(params["a"].value);  // negative for hyperbolic
                const G = params["G"].value;
                const M = params["M"].value;
                
                const tx = ma*Math.sqrt(a*a*a/(G*M));  // tx = t-t0
                return pgen("t", t0+tx);
            }
    );
    

    /**
     * t0: The time of periapsis passage.
     * 
     */
    gadd("t0", ["t", "MA", "Per"],
            function (params) {
                const per = params["Per"].value;
                if (per === Number.POSITIVE_INFINITY) {
                    return null;  // Hyperbolic orbit, need eccentricity
                }
                const t = params["t"].value;
                const ma = params["MA"].value;
                const t0 = t - ma / (2 * Math.PI) * per;
                return pgen("t0", t0);
            }
    );
    
    gadd("t0", ["t", "MA", "Per", "a", "G", "M"],
            function (params) {
                // To get here, we must have had the period be infinite in
                // the previous generator, so we have a hyperbolic orbit.
                // Don't need the period, but it's a needed argument
                // to ensure that precondition is met.
                const t = params["t"].value;
                const ma = params["MA"].value;
                const a = Math.abs(params["a"].value);  // negative for hyperbolic
                const G = params["G"].value;
                const M = params["M"].value;
                
                const tx = ma*Math.sqrt(a*a*a/(G*M));  // tx = t-t0
                return pgen("t0", t-tx);
            }
    );

    /**
     * TA: The true anomaly.  This is the angle between the periapsis and the position of the
     * object measured from the origin (which is at one of the focuses of the orbit.
     */
    gadd("TA", ["EA", "Ecc"],
            function (params) {

                // The true anomaly is related to the mean anomay by:
                // tan (TA/2) =  (1+e)/(1-e)  tan (EA/2)
                //   [replacing tan(EA/2), with tanh(EA/2) for
                //    hyperbolic orbits]
                const e  = params["Ecc"].getScalar();
                const EA = params["EA"].value;
                
                var tan;
                if (e > 1) {
                    tan = Math.tanh(EA/2);
                } else {
                    tan = Math.tan(EA/2);
                }                    
                var val = Math.sqrt((1 + e) / Math.abs(1 - e)) * tan;
                val = 2 * Math.atan(val);
                
                if (e < 1 && val < 0) {
                    val += 2*Math.PI;
                }

                return pgen("TA", val);
            }
    );
    
    gadd("TA", ["r", "Ecc", "L"],
             function (params) {
                 // Just the angle between r and Ecc.  We need L to tell
                 // the range.
                 const r     = params["r"].value;
                 const e     = params["Ecc"].value;
                 const l     = params["L"].value;
                 const dot   = Util.dot(r, e)/(Util.mag(r)*Util.mag(e));
                 const cross = Util.cross(e,r);  // Order matters here.
                 var   angle = Math.acos(dot);
                 if (Util.dot(cross,l) < 0) {
                     angle = 2*Math.PI/angle;
                 }
                 return pgen("TA", angle);
             }
    );

    // v: the instantaneous velocity of the body.
    gadd("v", ["L", "Rot", "r", "a", "Ecc", "TA"],
        function(params) {
            const Lm  = params["L"].getScalar();
            const r   = params["r"].value;
            const rm  = Util.mag(r);
            const e   = params["Ecc"].getScalar();
            const a   = params["a"].value;
            const rot = params["Rot"].value;
            const ta  = params["TA"].value;
            
            const p   = a*(1-e*e);
            const nu  = rot[5];
            
            const sn  = Math.sin(ta);
            const cos = Math.cos(nu+ta);
            const sin = Math.sin(nu+ta);
            
            const vrad = Util.times(Lm*e*sn/(rm*p) , r);
                        
// 
// Six element vector with to make efficient position computations.
//                rot       = [0:co,  1:-so*ci,  // Coefficients for x 
//                             2:so,  3: co*ci,   // Coefficients for y
//                             4:si,             // Coefficients for z
//                             5:n]              // Save nu so all rotation information stored in rot.    
            const f    = Lm/rm;
            const vtan = [   -f*(rot[0]*sin - rot[1]*cos),
                             -f*(rot[2]*sin - rot[3]*cos),
                              f*rot[4]*cos ];
            const vtot = Util.vadd(vrad, vtan);
            return pgen("v", vtot);
        }
    );
    // Use any of the anomalies to get the velocity.
/*    gadd("v", ["G", "M", "r", "a", "L", "TA"],
            function (params) {
                const ta = params["TA"].value;
                return vcalc(params, ta);
            }
    );
    gadd("v", ["G", "M", "r", "a", "L", "EA"],
            function (params) {
                const ea = params["EA"].value;
                return vcalc(params, ea);
            }
    );
    gadd("v", ["G", "M", "r", "a", "L", "MA"],
            function (params) {
                const ma = params["MA"].value;
                return vcalc(params, ma);
            }
    );*/
}

export default LoadOrigins;
