import Util from '../std/Util.js';
import ParameterFactory from './ParameterFactory.js';

class Parameter {
    static  SCALAR = 0;
    static  VECTOR = 1; 
    static  MATRIX = 2;
    static  BLOB   = 3;  // Non-standard structure.
    
    constructor(name, value, dim, varies, renderer, unit) {
        this.name     = name;
        this.dim      = dim;
        this.varies   = varies;
        this.value    = value
        this.renderer = renderer;
        this.unit     = unit;
    }

    render() {
        if (!this.renderer) {            
            return this.value;
        }
        const val = this.renderer(this.value, this.unit);
        return val;
    }
    
    rerender() {
        const info = ParameterFactory.getParamInfo(this.name);
        const units = info.units;
        if (!units) {
            Util.log("No units for parameter");
            return;
        }
        var i;
        for (i=0; i<units.length; i += 1 ) {
            // If a unit was specified and they match,
            // or no unit was specified and the scale is 1
            // then we've found the current unit.
            if ((this.unit && (units[i].unit === this.unit.unit)) ||
                (!this.unit && (units[i].scale === 1))) {
                break;
            }
        }
        if (i > units.length) {
            Util.log("Unit not found in rerender: "+this.name+" "+this.unit.unit);
            return;            
        }
        i = (i+1) % units.length;
        this.unit = units[i];
        const res = this.render();
        return res;
    }
    setUnit(unit) {
        this.unit = unit;
    }
    
    getDim(dim) {
        return this.dim;
    }
   
    getName() {
        return this.name;
    }
    
    getType() {
        return this.dim;
    }
        

    getScalar() {
        switch (this.dim){
            case Parameter.SCALAR:
                return this.value;
                
            // For vector quantities return the magnitude of the vector.
            case Parameter.VECTOR:                
                return Util.mag(this.value);
                
            // For matrix quantities return the determinant
            // We assume 3d matrices.
            case Parameter.MATRIX:
                return Util.determinant3(this.value);
                
            default:
                throw new Error("Unsupported dimensionality "+this.dim+" for "+this.name);
        }
    }

    getVector() {
        if (this.dim === Parameter.VECTOR  || this.dim === Parameter.BLOB) {
            return this.value;
        } else {
                throw new Error("Vector request for non-vector parameter "+this.name); 
        }
    }
    
    getMatrix() {
        if (this.dim === Parameter.MATRIX) {
            return this.value;
        } else {
            throw new Error("Matrix request for non-matrix parameter "+this.name);
        }
    }

    isScalar() {
        return this.dim === Parameter.SCALAR;
    }
    
    isVector() {
        return this.dim === Parameter.VECTOR;
    }
    
    isMatrix() {
        return this.dim === Parameter.MATRIX;
    }
}

export default Parameter;
