import Animation from "../elems/Animation.js";
import Util from "../std/Util.js";

import Dump from "../std/Dump.js";

import {Scene, Vector3, Euler, DirectionalLight, AmbientLight} from "three";

class Satellite extends Animation {
    constructor(params) {
        super(params);
        
        this.delta         = 20;
        this.longClickTime = 1000;  // A little shorter since
                                    // we do less clicking here.

        this.scene       = new Scene();
        this.textShowing = true;
        this.centerBody  = this.name;
        
        this.cameraPos = new Vector3(0,0,2.5);
        this.cameraRot = new Euler(0,0,0,"XYZ");
        this.baseAnimation = this.primary;
        
        const here = this; 
        
        const r0 = this.planetOrbit.position(this.time);

        const sunlight = new DirectionalLight(0xffffff, 1);
        sunlight.position.x = -1000*r0[0];
        sunlight.position.y = -1000*r0[1];
        sunlight.position.z = -1000*r0[2];
        
        
        this.addElement("sunlight", {name: "sunlight", component: sunlight,
                         render: function(time) {
                             const r0 = here.planetOrbit.position(time);
                             
                             sunlight.position.x = -1000*r0[0];
                             sunlight.position.y = -1000*r0[1];
                             sunlight.position.z = -1000*r0[2];
                         }}, true);
                     
        this.scene.add(new AmbientLight(0x333333));

        var radius = 1;
                
        this.addElement(this.name+"Body", this.createSatellite(this.name+"Body", radius, this.texture));
        this.addTimeDisplay();                       
    }
    
    createSatellite(name, radius, texture) {
        const moon = this.createSphere(radius, this.segments, texture, null);
        moon.meta = {...this.meta, name: this.name, type: "moon"};
        const here = this;
        return {
            name:      name+"Body",
            component: moon,
            render:    function(time) {
                let r = here.satelliteOrbit.position(time);
                // Assume tidally locked (but initial phase would be nice too!)
                let phase = Math.atan2(r[1], r[0]);
                moon.rotation.y = phase; 
            }
        }      
    }
    
    doLongClick(event)  {
        
        this.textShowing = !this.textShowing;
        var text = "<hr/>";
        if (this.textShowing) {
            text += "Satellite: "+this.name+"<br>";
            text += "Source:    "+this.texture.src;
        }
        this.setQuickHTML(text);
        
        super.doLongClick(event);
    }    
}

export default Satellite;
