import Util from "../std/Util.js";

const Settings = {};

function parseURL() {
    Util.log("Parsing settings");

    const url = window.location.href;
    if (url.indexOf("?") >= 0) {
        let data = url.substring(url.indexOf("?")+1);
        let flds = data.split("&");
        for (let i=0; i<flds.length; i += 1) {
            let token = flds[i];
            if (token.indexOf("=") > 0) {
                let keyVal = token.split("=", 2);
                let key = decodeURIComponent(keyVal[0]);
                let val = decodeURIComponent(keyVal[1]);
                Settings[key] = val;                
            } else {
                token = decodeURIComponent(token);
                Settings[token] = true;
            }
        }
    }
}

parseURL();

export default Settings;


