

import  {Component, createRef}  from "react";
import Util             from "../std/Util.js";
import Constants        from "../std/Constants.js";
import ParameterFactory from "../orbits/ParameterFactory.js";
import Parameter        from "../orbits/Parameter.js";
import Orbit            from "../orbits/Orbit.js";
import Missions from "../solarsystem/Missions.js";
import Kepler  from "../orbits/Kepler.js";

import UnitLegend       from "./UnitLegend.js";

import Row       from "./Row.js";
import status    from "./Status.js";
import TextInput from "../elems/TextInput.js";



class Debug extends Component {
    
    constructor(props) {
        super(props);
    }
    
    diffCalc(r1, r2) {
        let diff = [r1[0]-r2[0], r1[1]-r2[1], r1[2]-r2[2]];
        let res = Util.mag(diff)/Math.sqrt(Util.mag(r1)*Util.mag(r2));
        res = res.toFixed(6);
        return res;
    }

    // It seems like the first time we create the objects we 
    // can get proper references to them, but later entries don't
    // seem to work.  So we only compute the references the first
    // time.  Probably something dodgy going on here.
    render() {
        Util.log("Start 1");
        let ecc  = 1.318976;
        let ma   = 63.07526296164796;
        Util.log("Limit is:"+Math.acos(-1/ecc));
        Util.log("Value is:"+Kepler(ma, ecc));
        Util.log("Time of encounter:"+Util.datetime("1980-11-01T00:00:00"));
        let dr = Math.PI/180;
        let str = "";
        let miss = Missions(0);
        str += "Missions:"+miss+"\n";
        str += "Length:"+miss.length+"\n";
        let voy = miss[1];
        str += "Got voyager2:"+voy+"\n";
        str += "Name: "+voy.name+" "+voy.component+"\n";
        let chain = voy.chain;
        
        let time = Util.datetime("1986-03-30T00:00:00");
        let p = chain.position(time);
        str += "Time:"+time+" "+p+"\n";
        str += "Index is :"+chain.index+"\n";
        time = Util.datetime("1986-01-01T00:00:00");
        str += "Time early:"+time+" "+p+"\n";
        str += "Index is :"+chain.index+"\n";
        for (let i=0; i<2000; i += 1) {
            str += i+" "+time +":"+chain.position(time)+" \n";
            time += 86400;
        }
        return (
        <pre> {str} </pre>        
               );
    }    
}

export default Debug;