import Util from "../std/Util.js";

class SecondaryOrbit {
    constructor(primary, secondary, secScale) {
        this.prime = primary;
        this.sec   = secondary;
        this.scale = secScale;
    }
    
    position(time) {
        let p = this.sec.position(time);
        if (this.scale) {
            p = Util.times(this.scale, p);
        }
        return Util.vadd(this.prime.position(time),p);
    }
    
    state(time) {
        const pstate = this.prime.state(time);
        const sstate = this.sec.state(time);
        return [
            Util.vadd(pstate[0], sstate[0]),
            Util.vadd(pstate[1], sstate[1])
        ];
    }
}

export default SecondaryOrbit;

