class TransformedOrbit {
    
    constructor(orbit) {
        this.orbit = orbit;
        this.transforms = [];
    }
    addTransform(trans) {
        this.transforms.push(trans);
    }
    position(time) {
        return this.transform(this.orbit.position(time));
    }
    
    transform(r) {
        for (let i=0; i<this.transforms.length; i += 1) {
            r = this.transforms[i].vTransform(r);
        }
        return r;
    }
    
    orbitXYZ(n) { 
        let vec = this.orbit.orbitXYZ(n);
        return this.multi(vec);
    }
    
    state(time) {
        // State just returns two vectors, r and v
        return multi(this.orbit.state(time));
    }
    
    multi(vec) {
        for (let i=0; i<vec.length; i += 1) {
            vec[i] = this.transform(vec[i]);
        }
        return vec;
    }
}

export default TransformedOrbit;


