import Animation from "../elems/Animation.js";
import Util from "../std/Util.js";
import Constants from "../std/Constants.js";

import Satellite from "../animations/Satellite.js";


import CoordTrans       from "../std/Coords.js";
import Orbit            from "../orbits/Orbit.js";
import TransformedOrbit from "../orbits/TransformedOrbit.js";
import Planet           from "../solarsystem/Planet.js";
import ParameterFactory from "../orbits/ParameterFactory.js";
import SolarPlanets     from "../solarsystem/SolarPlanets.js";
import ssData           from "../data/PlanetaryData.js";
import Satellites       from "../earth/Satellites.js";
import NoradReader      from "../earth/NoradReader.js";

import {Scene, Vector3, Euler, AmbientLight, DirectionalLight} from "three";
import {Mesh, Color, MeshPhongMaterial} from "three";

class PlanetarySystem extends Animation { 

    constructor(planet, params) {
        Util.log("Creating new planetary system: "+planet);
        super(params);
        
        let info = ssData.Sol.planets[planet];
        

        this.delta         = 2;
        this.moons         = {};
        this.longClickTime = 1000;  // A little shorter since
                                    // we do less clicking here.

        this.scene          = new Scene();
        this.name           = planet;
        this.planetName     = planet;
        this.centerBody     = planet;
        this.rotAxis        = info.rotAxis;
        this.rotationPhase  = info.rotationPhase;
        this.rotationPeriod = info.rotationPeriod;
        this.moonInfo       = info.moonInfo;
        this.texture        = info.render.texture;
        this.ring           = info.ring;
        this.satelliteURL   = info.satelliteURL;
        this.sceneCoord     = "PEq";
        
        this.cameraPos     = new Vector3(0,0,5);
        this.baseAnimation = "Solar System";
        
        this.segments = 40;
        const here = this;
        // 
        // For displaying a planetary system we want
        // the reference plane to be the equatorial plane of the
        // planet.
        this.eceqE = CoordTrans.getCoords("eceq");
        
        // transformation from Earth equatorial to planet equatorial
        let  angs = [this.rotAxis.ra, this.rotAxis.dec, Math.PI/2];
        this.eqEeqP = CoordTrans.getCoords(angs);
        // Needed for identifying position in planetary systems.
        this.eqPeqE = CoordTrans.getCoords([Math.PI/2, this.rotAxis.dec, this.rotAxis.ra]);
        
        this.planetOrbit = this.getOrbit(this.planetName, this.time);
        
        // Convert to local equator.
        this.planetOrbit = new TransformedOrbit(this.planetOrbit);
        this.planetOrbit.addTransform(this.eceqE);
        this.planetOrbit.addTransform(this.eqEeqP);        
        
        const r0         = this.planetOrbit.position(this.time);

        const sunlight = new DirectionalLight(0xffffff, 1);
        sunlight.position.x = -1000*r0[0];
        sunlight.position.y = -1000*r0[1];
        sunlight.position.z = -1000*r0[2];
        sunlight.castShadow = true;        
        let sunl = {name: "sunlight", component: sunlight,
                         render: function(time) {
                             const r0 = here.planetOrbit.position(time);
                             
                             sunlight.position.x = -1000*r0[0];
                             sunlight.position.y = -1000*r0[1];
                             sunlight.position.z = -1000*r0[2];
                         }};
        this.addElement("sunlight", sunl, true);
                     
        this.scene.add(new AmbientLight(0x333333));

        var radius   = 1;
        let centralBody = this.createPlanet(this.planetName, radius, this.texture, this.ring);
        centralBody.component.meta = {...info, name: this.planetName, type: "planet"};
        this.addElement(this.planetName, centralBody);
        this.addTimeDisplay();
        
        if (this.moonInfo) {
            this.addMoons(this.moonInfo);
        }
        
        if (this.satelliteURL) {
            NoradReader(this.satelliteURL, this.addSatellites.bind(this));
        }
    }
        
        
    addMoons(moonInfo) {
        for (let moonName in moonInfo) {
            const md   = moonInfo[moonName];
            let   morb = this.getMoonOrbit(moonName, md);
            if (md.frame && md.frame.type != "Equatorial") {
                // Need to tranform the frame of the orbit from
                // the frame in which it was computed, into the
                // equatorial frame of the planet.
                morb = new TransformedOrbit(morb);
                if (md.frame == "Ecliptic" ) {
                    // Same transformation as for planet orbit.
                    morb.addTransform(this.eceqE);
                    morb.addTransform(this.eqEeqP);
                } else if (md.frame == "Laplace") {
                    // Need the transform from Earth
                    // equatorial to the Laplace frame.
                    const lapTrans = CoordTrans.getCoords([md.frame.ra,md.frame.dec,Math.PI/2]);
                    morb.addTransform(this.eceqE);
                    morb.addTransform(lapTrans);
                }
            }
            var rad = 0.2;
            if (md.render && md.render.frac) {
                rad = md.render.frac;
            }
            var moon;
            var color = md.render.color? md.render.color: 0xFFFF00;
            
            moon = Planet(moonName, this.time, 
                          morb, rad, color, false, true);
            let meta = {...moonInfo[moonName], name: moonName, type: "moon", orbit: morb};
            moon.component.meta = meta;
                          
            if (md.render && md.render.texture) {
                // Replace the featureless sphere
                let pos = morb.position(this.time);
                let texturedSphere = this.createSphere(rad, this.segments, md.render.texture);               
                texturedSphere.position.set(pos[0], pos[1], pos[2]);
                moon.component = texturedSphere;
                moon.component.meta = meta;
            }
            
            this.addElement(moonName+"Body", moon);
            this.moons[moonName] = {rendering: moon, data: md, orbit: morb};
        }

    }
    
    createPlanet(name, radius, texture, ring) {
        const planet = this.createSphere(radius, this.segments, texture, ring, name);
        const here = this;
        return {
            name:      name,
            component: planet,
            render:    function(time) {
                let phase = here.rotationPhase + 2*Math.PI * (time/here.rotationPeriod);
                phase %= 2*Math.PI;
                planet.rotation.y = phase; 
            }
        }      
    }
    
    getOrbit(name, time) {
        const pData = SolarPlanets.getElements(name, time);
        const elems = pData.elements;
        this.planetRadius = pData.diameter*1000/2;  // diameter in km to radius in m.
        this.sceneScale   = this.planetRadius;
        
        const orb = new Orbit(name);

        for (var key in elems) {
            orb.addParameter(ParameterFactory.genParam(key, elems[key]));
        }
        
        orb.addParameter(ParameterFactory.genParam("t", time));
        // Make sure we can get positions.
        const r0 = orb.getParameter("r", true);;        
        
        return orb.quickOrbit();
    }
    
    getMoonOrbit(name, moonData) {
        let  morb = new Orbit(name);
        const mElems = moonData.elems;
        if (!mElems.t) {
            if (moonData.prec) {
                mElems.t = moonData.prec.ep0;
            } else {
                mElems.t = this.time;
            }
        }
        morb.addStructure(moonData.elems);
        const rm = morb.getParameter("r", true);
        morb = morb.quickOrbit();  // Just get this...
        if (moonData.prec) {
            morb.addPrecession(
                moonData.prec.ep0,
                moonData.prec.fOmega,
                moonData.prec.fNu
            );
        }
        morb.scale = 1/this.planetRadius;
        return morb;
    }
    
    find(name, callback) {
        let here = this;
        function find2(found) {
            if (!here.satelliteData) {
                callback(found);
                return;
            }
            let regstr = "^"+name.replaceAll("*", ".*")+"$";
            let regex = new RegExp(regstr, "i");
            
            for (let i=0; i<here.satelliteData.length; i += 1) {
                let sat = here.satelliteData[i];
                if (sat.name.match(regex)) {
                    let meta = {...sat, type:"satellite"};
                    found.push({meta: meta, distance: 0});
                }
            }
            callback(found);
        }
        super.find(name, find2);
    }
    
    addSatellites(epoch, data) {
        Util.log("Epoch for satellites: "+epoch/(365.25*86400));
        Util.log("Number of satellites: "+data.length);
        this.baseTime = epoch;
        this.satelliteData = data;
        this.setTime(epoch);
        this.addElement("satellites", Satellites(data, epoch));
    }

    doLongClick(event) {
      
        const matched = this.frame.raycast(event);
        let found = null;
        
        for (let i=0; i<matched.length; i += 1) {
            let meta = matched[i].meta;
            if (meta.type == "moon" && this.moonInfo[meta.name].render.texture) {
                this.animateMoon(meta.name);
                return;
            }
        }
        super.doLongClick(event);
    }
    
    animateMoon(name) {
        let anim = new Satellite({
          frame: this.frame,
          timeID: this.timeID,
          name: name,
          planet: this.name,
          planetOrbit: this.planetOrbit,
          primary: this.name,
          satelliteOrbit: this.moons[name].orbit,
          meta: ssData.Sol.planets[this.name].moonInfo[name],
          sceneScale: this.moons[name].radius,
          texture: this.moonInfo[name].render.texture
        });
        this.frame.setAnimation(anim);
        this.setQuickHTML("");
        return;
    }
}

export default PlanetarySystem;