import Constants from        "../std/Constants.js";
import Util      from        "../std/Util.js";

import QOrb      from        "../orbits/QOrb.js";
import ParameterFactory from "../orbits/ParameterFactory.js";
import Parameter from        "../orbits/Parameter.js";

class Orbit {
    
    
    constructor (name) {
        this.name       = name;
        this.parameters = {};
    }
    
    getOrbitName() {
        return this.name;
    }
    
    setOrbitName(newName) {
        this.name = newName;
    }
        
    /** Reset the orbit to completely undefined */
    clear() {
        this.parameters = {};
    }
    
    /** Clear 0 or more parameters */
    clearParameters(...params) {
        if (params && params[0]) {
            params = params[0];
        } else {
            return;
        }
        for (var i=0; i<params.length; i += 1) {
            delete this.parameters[params[i]];
        }
    }
    
    /** Known variable parameters, i.e., vary along the orbit */
    variables() {
        return this.conOrVar(true);
    }
    
    /** Known orbital constants */
    constants() {
        return this.conOrVar(false);
    }
    
    conOrVar(flag) {
        const have = Object.keys(this.parameters);
        const constants = [];
        const params = this.parameters;
        have.forEach(                
            function(name) {
                if (params[name].varies === flag) {
                    constants.push(name);
                }
            }
        );
        return constants;        
    }

    /** Allow updates to varying parameters */
    update(name, value) {
        if (this.parameters[name]) {
            if (this.parameters[name].varies) {
                this.parameters[name].value = value;
            }
        }
    }
    
    /** Create and add a parameter.  Note that unit is often null */
    addParam(name, value, unit) {
        Util.log("Adding:"+name+" "+value+" "+unit);
        const info = ParameterFactory.getParamInfo(name);
        if (info == null) {
            Util.log("Attempt to generate invalid parameter:"+name);
        } else {
            this.addParameter(new Parameter(name, value, info.dim, info.varies, ParameterFactory.baseRender, unit));
        }
    }
    
    /** Add the specified parameter to the list of known parameters */
    addParameter(p) {
        const name = p.getName();
        if (this.parameters[name]) {
            Util.log("Replacing existing parameter:" + name);
        }
        this.parameters[name] = p;        
    }
    
    /** Add a structure of parameters into the orbit */
    addStructure(str) {
        for (let name in str) {
            this.addParameter(ParameterFactory.genParam(name, str[name]));
        }
    }
    

    /** Return the specified parameter.
     *  If it is already known just return it.
     *  If not see if we can try to calculate it,
     *  either immediately or after seeing what
     *  other parameters are calcuable.
     */
    getParameter(name, cycle) {
        
        // Do we already know this parameter?
        if (this.parameters[name]) {
            return this.parameters[name];
            
        }
        
        
        // Is this a knowable parameter
        if (ParameterFactory.getParamInfo(name) == null) {
            Util.log("Attempt to retrieve illegal parameter:"+name);
            return null;
        }                

        // Start a cycle where we look at all of the currently
        // unknown parameters and see if we can compute one.
        // If we can then we'll try to see if we can compute
        // the desired parameter. If we are unable to compute any parameters
        // then we stop, otherwise we go until we find we can't compute any more.
        var updateParam = true;
        while (updateParam) {
            
            // May have computed it below..
            if (this.parameters[name]) {
                return this.parameters[name];
            }
            
            
            // Can we compute the parameter we want now?
            var pn = this.newComputable(name);
            if (pn != null) {
                // We could.  So just return it!
                this.parameters[name] = pn;
                return pn;
            }
            
            // Not found yet.  Do we want to cycle and try again?
            if (!cycle) {
                return null;
            }
            
            // Couldn't.  Try to compute any unknown parameter.
            // If we are successful, then maybe that was the missing
            // prerequiste for what we want and we can try again.
            updateParam = false;
            var names = ParameterFactory.getNames();
            for (var i=0; i < names.length; i += 1) {
                const param = names[i];
                pn = this.newComputable(param);
                if (pn != null) {
                    updateParam = true;
                    this.parameters[param] = pn;
                } else {
                }               
            }
            // If updateParam is still false, then we
            // can't compute any more and we'll just exit the loot and return.
        }
        
        var knowns = Object.keys(this.parameters).sort();
        
        Util.log("Unable to find antecedents to parameter:"+name+" with known parameters:"+knowns);
        return null; 

    }
    
    
    /** If the parameter is unknown but computable, return the computed value. */
    newComputable(name) {
        
        if (this.parameters[name]) {
            return null;
        }
        

        // If knowable but not yet known then see if we can compute it...
        return ParameterFactory.deriveParameter(name, this.parameters);
    }
    
    
    /** Return a copy of the orbit */
    copy() {
        var c = new Orbit(this.name);
        // Copy in the parameters.
        for (var key in this.parameters) {
            c.parameters[key] = ParameterFactory.genParam(key, this.parameters[key].value);
        }
        return c;        
    }
    
    /** A quickorbit saves just the minimal information to compute the position or velocity
     *  as a function of time.
     */
    quickOrbit() {
        
        if (this.parameters["Rot"] && this.parameters["Ecc"] &&
            this.parameters["a"]   && this.parameters["Per"]) {
            const params = {
                rot: this.parameters["Rot"].value,   // This encodes the three orbit angles.
                e:   this.parameters["Ecc"].getScalar(),
                per: this.parameters["Per"].value,  
                a:   this.parameters["a"].value,
            }
            
            // May only know shape of orbit, not phase.
            // OrbitXYZ should still work.
            if (this.parameters["t0"]) {
                params["t0"] = this.parameters["t0"].value;
            }
            
            if (params.e < 1) {
                if (this.parameters["Per"]) {
                    params.per = this.parameters["Per"].value;
                } else {
                    Util.log("No period given for elliptic orbit");
                    return null;                    
                }
            } else {
                // While the true period is infinite, we need
                // something that scales MA to time.
                const a  =  params.a;
                const G  =  this.getParameter("G").value;
                const M  =  this.getParameter("M").value;
                params.per = 2*Math.PI * Math.sqrt(-(a*a*a)/(G*M));
            }
            
            const qorb = new QOrb(params);
            qorb.scale = 1./Constants.AU;
            
            return qorb;
            
        } else {
            
            Util.log("Attempt to get quick orbit with undefined constants");
            Util.log("   Available:"+Object.keys(this.parameters));
            return null;
        }
    }
}

export default Orbit;