import {RingGeometry, MeshBasicMaterial, Mesh} from "three";
const Ring = function(name, start, orbit, size, color){
 
    // We're going to make things smaller than the Sun, but 
    // can't scale directly or they will disappear...
    var ratio = (Math.log10(size/10000)+1)*.03;

    const geometry = new RingGeometry(1.1*ratio, 2*ratio, 8);
    const material = new MeshBasicMaterial({wireframe: true, color: color });
    const ring     = new Mesh(geometry, material);
    const p        = orbit.position(start);
    ring.position.set(p[0], p[1], p[2]);
    
    return {
      component: ring,
      name: name+"Ring",        
      render:function(time) {
          // Update planet positions.
           var r = orbit.position(time);
           ring.position.set(r[0], r[1], r[2]);
      }
  };
}

export default Ring;

