import Util         from "../std/Util.js";
import ChainedOrbit from "../orbits/ChainedOrbit.js";

import {ConeGeometry,MeshBasicMaterial,Mesh, Color} from "three";

function Missions(missions,start) {
    let results = [];
    for (let miss in missions) {
        let chain = ChainedOrbit.createChain(miss, missions[miss]);
        let time = chain.startTime;
        const geometry = new ConeGeometry(.02, 0.04, 8);
        const material = new MeshBasicMaterial({wireframe: false, color: new Color(missions[miss].color)})
        const sphere   = new Mesh(geometry, material);
        const p        = chain.position(start);
        sphere.position.set(p[0], p[1], p[2]);        
        let count = 0;
        sphere.meta = {...missions[miss],
            name: miss,
            type: "mission",
            orbit: chain
        }
        // Missions may be hidden at the center of the Sun.
        // So we aren't supposed to find them in the animation if their
        // radius is very small (probably could just check for = 0).
        let animHidden = 
           function() {
               return this.position.length() < 1.e-10;
           };
        animHidden = animHidden.bind(sphere);
        sphere.animHidden = animHidden;
    
        const ret = 
            {component: sphere, name: miss, chain: chain,
        
             render: function(time) {
              // Update planet positions.
                var r = chain.position(time);
                
                  // Using ret.component vs just sphere
                  // allows us to replace the component externally.
                  ret.component.position.set(r[0], r[1], r[2]);
        }};
        results.push(ret);

    } 
    return results;
}

export default Missions;