import {Component} from "react";
import Util from "../std/Util.js";

class ClickSpan extends Component {
    constructor(props) {
        super(props);
        this.click = props.click;
        this.value = props.value;
        this.text  = props.text;        
    }
    render() {
        return <button onClick={this.clickFunc.bind(this)}> {this.text} </button>;
    }
    clickFunc() {
        this.click(this.value);
    }
}

export default ClickSpan;

