import  {SphereGeometry,MeshBasicMaterial,Mesh} from "three";

const Sun = function(settings) {

    if (!settings) {
        settings = {};
    }
    const sunSize     = settings.sunSize      || 0.1;
    const sunColor    = settings.sunColor     || 0xFFFF00;
    const sunWire     = settings.sunWire      || false;
    const sunSegments = settings.sunSegements || 8;
    const sunName     = settings.sunName      || "SunBody";
    const sunPosition = settings.sunPosition  || [0,0,0];
        
    var geometry = new SphereGeometry(sunSize, sunSegments, sunSegments);
    var material = new MeshBasicMaterial({wireframe: sunWire, color: sunColor});
    var sphere   = new Mesh(geometry, material);
 
    sphere.position.set(sunPosition[0], sunPosition[1], sunPosition[2]);
    sphere.meta = {
        name: "Sun",
        type: "star",
        spt: "G2V",
        x:0,
        y:0,
        z:0,
        u:0,
        v:0,
        w:0
    };
    
    return {component: sphere, name: sunName, 
           render: null};        
}

export default Sun;
    


