import Util from "../std/Util.js";
import {Component} from "react";

import OptionsSelector from "../elems/OptionsSelector.js";

class ActionHolder extends Component {
    constructor(props){        
        super(props);
        this.state          = {content: null};
        this.controls       = props.controls;
        this.state.optState = props.optState;
        this.state.junk     = "junk";
        this.anim           = props.anim;
    }
    
    componentDidMount() {
        // Give parent a reference down.
        this.controls.holder = this;
    }
    
    setAnimation(anim) {
        this.anim = anim;
        if (this.options) {
            this.options.setAnimation(anim);
        }
    }
    
    render() {
        
        if (this.state.optState) {
            if (this.state.content) {
                return <div>{this.state.content}</div>;
            } else {
                return <OptionsSelector anim={this.anim} holder={this} />;
            }
        } else {
            return <div></div>;
        }
    }    
}

export default ActionHolder;

