import {Component, createRef} from 'react';

import Util           from "./std/Util.js";
import Settings       from "./std/Settings.js";

// Used in animations
import Frame          from "./elems/Frame.js";
import UserControls   from "./elems/UserControls.js";
import animFactory    from "./animations/AnimationFactory.js";
import Debug          from "./calc/Debug.js";

// Used in OrbitCalculator
import OrbitCalculator from "./calc/OrbitCalculator.js";

class App extends Component {
    
    constructor(props) {
        super(props);
        
        // The use of the app setting is a kludge to allow us to define multiple pages
        // within the react framework easily.  While this may nominally
        // require the download of the entire JS code for the
        // OrbitCalculator, this will normally be cached in the browser
        // already.  Their is a lot of mystery about routing and such,
        // but it is unclear if that will allow us to serve two different
        // pages simultaneously and in any case requires higher level
        // wizardry than I am capable of.
        this.appKey   = Settings["app"];
        const url     = window.location.href;
        this.controls = createRef();
        
        if (this.appKey) {
            return;
        }
        this.frame = null;
    }
    
    onWindowResize() {
        if (this.appKey) {
            return;
        }
        const dx = window.innerWidth;
        const dy = window.innerHeight;
        this.frame.resize(dx, dy);
    }
    
    
    render() {
        if (this.appKey) {
            if (this["render"+this.appKey]) {
                return this["render"+this.appKey]();
            }
            return;
        }
        
        const dx = window.innerWidth;
        const dy = window.innerHeight;
        const me = this;
        return (
            <div>    
    <img className="logo1 animated fadeOut" src="images/program/EVERYTHING.png" width="578" height="176"
         onAnimationEnd={function() {me.deleteAfterAnimation('logo1');}} />
    <img className="logo2 animated2 fadeInOut" src="images/program/inbetween.png"  width="548" height="40" 
         onAnimationEnd={function() {me.deleteAfterAnimation('logo2');}} />
    <img className="logo3 animated3 fadeInOut" src="images/program/fourd.png" width="391" height="40" 
         onAnimationEnd={function() {me.deleteAfterAnimation('logo3');}} />

               <div style={{
                  height:dx+"px", 
                  width:dy+"px"
                }} id="baseCanvas"></div>
               <UserControls ref={this.controls} />
            </div>
        );
    }

    // Delete logo lines after their initial animation.
    deleteAfterAnimation(cls) {
        let elems = document.getElementsByClassName(cls);
        for (var i=0; i<elems.length; i += 1) {
            let elem = elems[i];
            let parent = elem.parentElement;
            parent.removeChild(elem);
        }
    }
    
    renderDebug() {
        return <Debug />
    }
    
    renderOrbParams() {
        return <OrbitCalculator ref={this.controls} />;
    }
    
    componentDidMount() {
        if (this.appKey) {
            if (this["componentDidMount"+this.appKey]) {
               this["componentDidMount"+this.appKey]();
               return;                
            }
            return;
        } else {
            window.addEventListener( 'resize', this.onWindowResize.bind(this) );
            // Note injection of animFactory into the Frame.  This resolves
            // an import loop between the animations and the animation factory
            this.frame = new Frame({animFactory: animFactory, animDiv: "baseCanvas", size:[window.innerWidth, window.innerHeight]});
            
            this.solar = animFactory.getAnimation("Solar System", {timeID:"timeDisplay", frame:this.frame});
            
            this.frame.setUserControls(this.controls.current);
            this.frame.setAnimation(this.solar);
        }
        window.GLOBALS = {animFactory: animFactory};
    }
    
    componentDidMountOrbParams() {
        // This kludgey bit allows the calculator to send
        // orbits to the solar system even though it's on
        // a different page.
        let win = window.opener;
        if (win) {
            this.controls.current.setAnimFactory(win.GLOBALS.animFactory);
        }
    }
    
    componentDidMountDebug() {
        
    }
}

export default App;
