//    https://nssdc.gsfc.nasa.gov/planetary/factsheet/
// Satellite orbits:
//    https://ssd.jpl.nasa.gov/?sat_elem
// Radius of moon:
//    https://nssdc.gsfc.nasa.gov/planetary/factsheet/moonfact.html
// Radius Phobos and Deimos:
//    Wikipedia
// Pluto Moons
//    Wikipedia seems consistent but believe other source was used.
// Solar data
//    Wikipedia
// Planetary rotation periods and location of rotation poles
//    Wikipedia
// A variety of surces are used for the images (aka textures) of the planets
// and moons.  Each image should indicate the source.

// Structure:
// {star :
//   ...stellar data
//   smallBodiesURL: small bodies data
//   planets: {
//      planet:
//         ... planet data 
//         mooninfo: {
//            satellite: {... satellite data... },...
//         }
//      },...
//   missions: {
//      mission: {... mission data}, ...
//   }
// }
// Units are mostly MKS except for the elements of the planetary orbits
// which are converted in SolarPlanets code.  Also the values of N0 (in
// satellite orbits) are in orbits/day.

