import {LineBasicMaterial, BufferGeometry,Line, Vector3} from "three";

// This returns an orbit with the distances scaled to AU by default.
const OrbitPlot = function (name, orbit, color, t) {
    const orbitR = orbit.orbitXYZ(360, t);
    const points = [];
    for (var pi=0; pi<orbitR.length; pi += 1) {
        const r = orbitR[pi];
        points.push(new Vector3(r[0], r[1], r[2]));
    }
   
    const material = new LineBasicMaterial( { color: color } );
    const geometry = new BufferGeometry().setFromPoints( points );
    const line     = new Line(geometry, material );
    return {component: line, name:name+"Orbit", render: null};
}

export default OrbitPlot;