import Util       from "../std/Util.js";
import ssData     from "../data/PlanetaryData.js";
import CoordTrans from "../std/Coords.js";
import Constants  from "../std/Constants.js";

import Dump from "../std/Dump.js";

let showOffset = true;
function crdMsg(center, csys, unit, scale, x, y, z, trans) {
    let dist = Math.sqrt(x*x+y*y+z*z);
    
    let lon  = Math.atan2(y, x);
    if (lon < 0) {
        lon += 2*Math.PI;
    }
    
    let lat  = Math.asin(z / dist);
    let crds = CoordTrans.coordsSet(csys, lon, lat, trans);
    dist = dist*scale;
    let msg = "<br/>Dist. to "+center+": " + dist.toFixed(3) + " "+unit;
    msg = "<br/> Relative to "+center+": " + CoordTrans.posString(crds);
    return msg;
}

function getMoonMeta(entry, anim) {
    
    let meta = entry.meta;
    let primary = null;
    if (anim.moonInfo) {
        primary = anim.name;
    } else {
        primary = anim.primary;
    }
    let msg = "Name: " + meta.name;
    msg += "<br/>Primary: " + primary;
    if (meta.radius) {
        msg += "<br/>Radius: " + (meta.radius / 1000).toFixed(1) + " km";
    }
    let orbit = meta.orbit;
    if (orbit && orbit.orbit) {
        orbit = orbit.orbit;
    } else {
        if (meta.elems  && meta.elems.a && meta.elems.N0) {
            orbit = {a: meta.elems.a, per: 86400/meta.elems.N0};
        }
    }
    if (orbit) {
        msg += "<br/> Semimajor axis:" + (orbit.a / 1000).toFixed(1) + " km";
        msg += "<br/> Period: " +  (orbit.per/86400).toFixed(3) + " day";
    }
    return msg;
}

function getSatelliteMsg(entry, anim) {
    let flds = entry.meta.name.split("_");
    let msg = "Name: " + flds[0];
    let yr = flds[1].substring(0, 2);
    let c1 = yr.charAt(0);
    if (c1 < '5') {
        yr = '20' + yr;
    } else {
        yr = '19' + yr;
    }
    msg += "<br/>Launch year: " + yr;
    let time = anim.time;
    let orbit = entry.meta.orbit;
    
    let pos = orbit.position(time);
    let rad = Util.mag(pos);
    let ra = Math.atan2(pos[1], pos[0]);
    let dec = Math.asin(pos[2] / rad);
    let crds = CoordTrans.coordsSet("Eq", ra, dec);
    rad *= Constants.rEarth / 1000;
    let alt = rad - Constants.rEarth / 1000;
    msg += "<br/>NORAD id:" + entry.meta.id;
    msg += "<br/>Altitude: " + alt.toFixed(2) + " km";
    msg += "<br/>Semimajor axis: " + (orbit.a / 1000).toFixed(2) + " km";
    if (orbit.per < 86000) {
        msg += "<br/> Period: " + (orbit.per / 3600).toFixed(4) + " hr";
    } else {
        msg += "<br/> Period: " + (orbit.per / 86400).toFixed(4) + " days";
    }
    if (showOffset) {
        msg += "<br/>Relative to Earth center:" + CoordTrans.posString(crds);
    }
    return msg;
}

function getSmB(entry, anim) {
    let sd = entry.meta;
    let msg = "Name: " + sd.name;
    if (sd.H) {
        msg += "<br/>AbsMag: " + sd.H;
    }
    let descrip = null;
    let ot = sd.Orbit_type;
    if (ot == "P") {
        descrip = "Periodic comet";
    } else if (ot == "C") {
        descrip = "Long period comet";
    } else if (ot == "I") {
        descrip = "Extrasolar object";
    } else if (ot == "A") {
        descrip = "Cometary orbit/asteroid properties";
    } else if (ot == "MBA") {
        descrip = "Main belt asteroid";
    } else {
        descrip = ot;
    }
    if (descrip) {
        msg += "<br/>Type: " + descrip;
    }
    if (sd.Number) {
        let numb = sd.Number.substring(1, sd.Number.length-1);
        msg += "<br/> MPC#: "+numb;
    }

    if (sd.orbit.Per) {
        msg += "<br>Period: " + (sd.orbit.Per * 100 / Constants.CENTURY).toFixed(3) + "yr";
    }
    if (sd.orbit.a) {
        msg += "<br>Major axis:" + (sd.orbit.a / Constants.AU).toFixed(3) + " au";
    }
    let time = anim.time;
    let pos  = sd.orbit.position(time);
    if (showOffset) {
        msg += crdMsg("Sun", "Ec", "au", 1, pos[0], pos[1], pos[2]);
    }
    return msg;
}

function getPlanetMeta(entry) {
    let meta = entry.meta;
    let name = meta.name;
    let msg = "Name: " + name;
    msg += "<br/> Mass: " + meta.mass.toPrecision(4) + " kg";
    msg += "<br/> Semimajor axis: " + meta.elems.a.toFixed(4) + " au";
    msg += "<br/> Orbital Period: " + (360 / meta.derivs.ML * 100).toFixed(3) + " yr";
    msg += "<br/> Planet Radius: " + (meta.radius / 1000).toFixed(1) + " km";
    msg += "<br/> Rotation Period: " + (meta.rotationPeriod / 86400).toFixed(3) + " d";
    if (meta.moonInfo) {
        let count = 0;
        for (let moon in meta.moonInfo) {
            count += 1;
        }
        msg += "<br/> Number of moons: " + count;
    }
    return msg;
}

function getGalMsg(entry, anim) {
    let gal = entry.meta;
    let msg = "Name: "+gal.name;
    if (gal.bmag) {
        msg += "<br/>Bmag: "+gal.bmag;        
    }
    msg += "<br/> Distance: "+gal.d+" Mpc";
    msg += "<br/>Diameter: "+gal.diameter+" kpc";
    let types = ["S0", "S0", "S0", "S0a", "Sa", "Sab", "Sb", "Sbc", "Sc", "Scd", "Sd"];
    if (gal.morph_type) {
        let t = gal.morph_type;
        let type = "Unknown";
        if (t < -3) {
            type = "Elliptical";
        } else if (t > 7) {
            type = "Irregular";
        } else {
            type = types[t+3];            
        }
        msg += "<br/>Type: " + type + " (" + t + ")";
    }
    return msg;
}

function getStarMsg(entry, anim) {
    let msg = "Name: " + entry.meta.name;
    if (entry.meta.spt) {
        msg += "<br/>Spectral type: " + entry.meta.spt;
    }
    if (entry.meta.np) {
        msg += "<br/>Number of known planets: "+entry.meta.np;
    }
    let time = anim.time;
    let ent = entry.meta;
    const scl = time/30.856e12;
    let p = {x:ent.x, y:ent.y, z:ent.z};
    if (ent.u || ent.v || ent.w) {
        p.x += scl*ent.u;
        p.y += scl*ent.v;
        p.z += scl*ent.w;
    }
    let d = Math.sqrt(p.x*p.x + p.y*p.y + p.z*p.z);
    msg += "<br/> Dist: "+d.toFixed(2)+" pc, "+(d*3.26156).toFixed(2)+" ly";
    if (showOffset) {
        msg += crdMsg("Sun", "Ga", "pc", 1,
           p.x,p.y,p.z);
    }
    return msg;
}

function getMissionMsg(entry, anim) {
    let mission = ssData.Sol.missions[entry.meta.name];
    let msg = "Name: " + entry.meta.name;
    if (mission.purpose) {
        msg += "<br/> Purpose: " + mission.purpose;
    }

    if (mission.horizonsID) {
        msg += "<br/> Horizon ID:" + mission.horizonsID;
    }

    let start = mission.orbits[0].start;
    let end = mission.orbits[mission.orbits.length - 1].stop;
    let ys = 2000 + start * 100 / Constants.CENTURY;
    let ye = 2000 + end * 100 / Constants.CENTURY;
    if (ye > 9999.99) {
        ye = 9999.99;
    }
    msg += "<br/>Orbit followed: " + ys.toFixed(2) + " to " + ye.toFixed(2);
    let pos = entry.component.position;
    let dist = pos.length();
    if (dist > .001  && showOffset) {
        msg += crdMsg("Sun", "Ec", "au", 1, pos.x, pos.y, pos.z);
    } else {
        msg += "<br/> No orbit at current date";
    }
    return msg;
}
function InfoMsg(entry, anim) {

    let type = entry.meta.type;
    showOffset = true;
    if (type == "orbit") {
        showOffset = false;
        type = entry.meta.otype;
    }
    let name = entry.meta.name;
    let msg = null;
    if (type == "star") {
        
        if (name == "Sun" || name == "Sol") {
            msg = name + ": Spectral type: G2V<br/>Number of planets: 9";
            return msg;
            
        } else {
            return getStarMsg(entry, anim);
        }
    } else if (type == "galaxy") {
        return getGalMsg(entry, anim);
        
    } else if (type == "planet"  ||  type == "ring" ) {
        // Planet is always an element, but could be at origin of sim.
        msg = getPlanetMeta(entry);
        let pos  = entry.component.position;
        let dist = pos.length();
        if (dist > .001) {
            msg += crdMsg("Sun", "Ec", "au", 1, pos.x, pos.y, pos.z);
        }
        return msg;
        
    } else if (type == "moon") {
        msg = getMoonMeta(entry, anim);
        if (anim.moonInfo) {
            let pos = entry.component.position;
            let primary = anim.name;
            let scale = ssData.Sol.planets[primary].radius/1000;
            let trans = anim.eqPeqE;
            msg += crdMsg("planet", "PEq", "km", scale, pos.x, pos.y, pos.z, trans);
        }
        return msg;
    } else if (type == "asteroid" || type == "comet") {
        return getSmB(entry, anim);
        
    } else if (type == "mission") {
        return getMissionMsg(entry, anim);
        
    } else if (type == "satellite") {
        return getSatelliteMsg(entry, anim);
    }
    Util.log("Should be unreachable"); ;
}

export default InfoMsg;
