import Util from "../std/Util";

class CoordTrans {
    // Construct a transformation  between coordinate systems
    // using coordinates of the poles.
    // 
    // ang = [alphaG, deltaG, lonE]
    //    alphaG,deltaG  = longitude/latitude of the pole of 
    //    the destination system in the original system,
    //    lonE =  the longitude of the pole
    //    of the original system in the destination system.
    
    constructor(ang) {
        this.sin_dg = Math.sin(ang[1]);
        this.cos_dg = Math.cos(ang[1]); 
        this.alphaG = ang[0];
        this.deltaG = ang[1];
        this.lonE   = ang[2];
        
        
        // Transform the unit vectors to get the full transformation
        // matrix
        const vx = this.toVector(this.transform(0.,        0.));
        const vy = this.toVector(this.transform(Math.PI/2, 0.));
        const vz = this.toVector(this.transform(0.,        Math.PI/2));
        this.mat = [vx, vy, vz];
    }
    
    static RADIAN = Math.PI/180;
    static coordDefs = {
        
        eqgal: [192.859481, 27.128251, 122.931918],
        eqec:  [270.,       66.560709,  90.],
        galeq: [122.931918, 27.128251, 192.859481],
        eceq:  [ 90.000000, 66.560709, 270.],
        ecgal: [180.023217, 29.811440,  96.383971],
        galec: [ 96.383971, 29.811440, 180.023217]
    };
    static transformations = {};
    
    static getCoords(name_or_angles) {
        if (Array.isArray(name_or_angles)) {
            // User is specifying angles directly.
            // so names is really a
            return new CoordTrans(name_or_angles);
        }
        let name = name_or_angles.toLowerCase();
        
        if (!CoordTrans.transformations[name]) {
            if (!CoordTrans.coordDefs[name]) {
                Util.log("Unknown coordinate System:"+name);
            }
            CoordTrans.transformations[name] = new CoordTrans(Util.times(CoordTrans.RADIAN, CoordTrans.coordDefs[name]));
        }
        return CoordTrans.transformations[name];
    }
        
    // radians to radians
    transform(a,d) { 
        const cos_d = Math.cos(d);
        const sin_d = Math.sin(d);
        const cos_delta = Math.cos(a-this.alphaG);
        const sin_delta = Math.sin(a-this.alphaG);
        
        const sin_b = sin_d*this.sin_dg + 
                      cos_d*this.cos_dg*cos_delta;
        
              
        const b     = Math.asin(sin_b);  // Range is OK...
        const cos_b = Math.cos(b);
        
        const num   = cos_d*sin_delta;
        const denom = sin_d*this.cos_dg - cos_d*this.sin_dg*cos_delta;
        var l     = this.lonE - Math.atan2(num, denom);
        l %= 2*Math.PI;
        if (l < 0) {
            l += 2*Math.PI;
        }
        return [l,b];
    }
        
    
    // Degrees to degrees
    transformd(a,d) {        
        return Util.times(1/CoordTrans.RADIAN,
           this.transform(a*CoordTrans.RADIAN, d*CoordTrans.RADIAN)
        );
    }
    
    toVector(crd) {
        const a = crd[0];
        const d = crd[1];
        const vec =  [
            Math.cos(a)*Math.cos(d), 
            Math.sin(a)*Math.cos(d), 
            Math.sin(d)
        ];
        return vec;
    }
    
    vTransform(v) {
        if (!v.length || v.length != 3) {
            return null;
        }
        // Get the vector in the new coordinate system
        var result =    Util.times(v[0], this.mat[0]);
        result = Util.vadd(result, Util.times(v[1], this.mat[1]));
        result = Util.vadd(result, Util.times(v[2], this.mat[2]));
        return result;
    }
    
    // Given an input frame and coordinates,
    // return the coordinates in Equatorial, Ecliptic and Galactic
    // coordinates.  Also return in the planetary equatorial frame if
    // that is the input.
    static coordsSet(iFrame, ilon, ilat, eqPeqE) {
        // First convert to Equatorial coordinates.
        let eq = null;
        switch (iFrame) {
            case "Ga":
               eq = CoordTrans.getCoords("galeq").transform(ilon, ilat);
               break;
            case "Eq":
                eq = [ilon, ilat];
                break;
                
            case "Ec":
                eq = CoordTrans.getCoords("eceq").transform(ilon, ilat);
                break;
            
            case "PEq": 
                eq = eqPeqE.transform(ilon, ilat); 
                break;
            
            default:
              Util.log("Invalid input coordinates:"+iFrame);
              return null;
        }
        let arr = [ ["Eq", eq]];
        arr.push(["Ec", CoordTrans.getCoords("eqec").transform(eq[0], eq[1])]);
        arr.push(["Ga", CoordTrans.getCoords("eqgal").transform(eq[0], eq[1])]);
        if (iFrame == "PEq") {
            arr.push(["PEq", [ilon,ilat]]);
        }
        return arr;       
    }
    static renderCoords(frame, lon, lat) {
        if (frame != 'Eq') {
            let sp1 = "";
            if (lon < 10) {
                sp1 = "  ";
            } else if (lon < 100) {
                sp1 = " ";
            }
            let sp2 = "";
            if (lat > 0) {
                sp2 += " ";
            }
            if (Math.abs(lat) < 10) {
                sp2 += " ";
            }
            return frame+": "+sp1+lon.toFixed(5)+", "+sp2+lat.toFixed(5);
        } else {
            // Display to seconds
            let ra = lon/15;
            let raFlds  = CoordTrans.sexa(ra,   24);
            let decFlds = CoordTrans.sexa(lat, 360);
            let space = ' ';
            if (lat < 0) {
                space = '';
            }
            return frame+": "+
                    raFlds[0]+"h "+raFlds[1]+"m "+raFlds[2]+"s" +  ", "+
                    decFlds[0]+"d "+decFlds[1]+"' "+decFlds[2]+'"';
        }
    }
    
    static sexa(val, limit) {
        let sign = "";
        if (val < 0) {
            sign = "-";
            val = Math.abs(val);
        }
        let unit = Math.floor(val);
        let frac = val - Math.floor(unit);
        let sec  = 3600 * frac;
        sec     += 0.5;
        sec      = Math.floor(sec);
        let min  = Math.floor(sec/60);
        sec      = sec % 60;
        if (Math.abs(unit) < 10) {
            unit = sign+'0'+unit;
        } else {
            unit = sign + unit;
        }
        if (min < 10) {
            min = "0"+min;
        } else {
            min = ""+min;
        }
        if (sec < 10) {
            sec = "0"+sec;
        } else {
            sec = ""+sec;
        }
        return [unit,min,sec];
    }
    
    static posString(coords) {
        let retStr = "";

        for (let i=0; i<coords.length; i += 1) {
            let cval = coords[i];
            let cstr = CoordTrans.renderCoords(cval[0], cval[1][0]*180/Math.PI, cval[1][1]*180/Math.PI);
            retStr += "<br/>  "+cstr;
        }
        return retStr;
    }

}
export default CoordTrans;

function test() {
  for (var name in CoordTrans.coordDefs) {
    var coords = CoordTrans.getCoords(name);
    Util.log("Testing matrix transformation for: "+name);
    var testCoord = [Math.random(), Math.random()];
    var testVec   = coords.toVector(testCoord);
    var tranCoord = coords.transform(testCoord[0], testCoord[1]);
    var tranVec   = coords.vTransform(testVec);
    Util.log("Initial coords:"+testCoord);
    Util.log("Initial vec:"+testVec);
    Util.log("Transformed coords:"+tranCoord);
    Util.log("Transformed vector:"+tranVec);
    Util.log("Implied coordinates:",
       Math.atan2(tranVec[1], tranVec[0]),
       Math.asin(tranVec[2]));
  }
}
