import Util from "../std/Util.js";
import Constants from "../std/Constants.js";
import QOrb from "../orbits/QOrb.js";
import SecondaryOrbit from "../orbits/SecondaryOrbit.js";
import SolarPlanets from "../solarsystem/SolarPlanets.js";

import Dump from "../std/Dump.js";

// 
// // This class allows us to define a 
// chain of orbits to define the position
// of a object which is subject to changing conditions
// either due to the evolution of an orbit or active
// control.

// Note that this class does not inherit from existing
// orbit classes and only defines the 'position' method
// (we should add the state method ultimately if we need
// velocities).

// For each interval we define define a start time and
// stop time for which these elements are useful.  We may
// either assume the elements are exactly correct for the entire
// interval, or exactly correct for the beginning of the interval
// followed by a time in which we wish to linearly interpolate
// with the following set of elements.
// 
// We may anticipate discontinuities in the orbit if we do
// not include an interpolation interval.  (We can set
// the times such that the entire interval is interpolated).
////
// The behavior prior to the first or after the last interval
// can either extrapolate the extermum orbits, or we can return
// an object at the origin (which will normally be hidden behind something else)
// So if we wish to simulate a space mission beginning on some date we
// supply the orbit chain and indicate that we should return the origin for
// the prior times.  If the mission ends destructively we may do the same
// for the subsequent time.

class ChainedOrbit {
    constructor() {
        this.orbits     = [];    // The orbits in the chain
        this.name       = null;  // The name for this chain (e.g., the mission name)
        this.usePrior   = false; // Use the first orbit before its start time.
        this.useSubseq  = false; // Use the last orbit after its end time
        this.index      =  0;    // The index of the current orbit
        this.norbit     =  0;    // The number of orbits in the chain
        this.isPrior    = false; // We are currently prior to the first interval
        this.isSubseq   = false; // We are currently subsequent to the last interval
        this.interp     = false; // We are interpolating?
        this.startTime  = null;  // The start time of the first orbit in the chain
        this.stopTime   = null;  // The end time of the last orbit in the chain
        this.count      = 0;
    }
    
    orbitXYZ(n, t) {
        if (t == null || t < this.startTime || t > this.stopTime) {
            return null;
        } else {
            this.setOrbit(t);
            return this.orbits[this.index].orbit.orbitXYZ(n,t);
        }        
    }
    
    addOrbit(orbit, start, stop, interp, scale) {
                
        if (stop <= start) {
            Util.log("Invalid orbit chain interval:"+orbit.name+" "+this.norbits);
            return;
        }
        if (!interp) {
            interp = stop;
        } else if (interp < start || interp > stop) {
            Util.log("Invalid orbit chain interpolation time: "+interp+" not between "+start+" and "+stop);
            return;
        }
        
        
        if (this.norbit <= 0) {
            this.startTime = start;
        }
        
        if (this.norbit > 0) {
            if (start != this.stopTime) {
                Util.log("Orbit gap:"+this.name+" index:"+this.norbit, this.startTime, this.stopTime, start, stop);
                return;
            }
        }
        
        this.stopTime = stop;
        
        if (!this.name && this.orbit.name) {
            this.name = this.orbit.name;
        }
        
        this.orbits.push({start: start, interp: interp, stop: stop, orbit:orbit, scale: scale});        
        this.norbit = this.orbits.length;
    }
    
    // Do we extend the initial orbit prior
    // to its start time?  If not the
    // orbit will return position and velocity
    // as 0.
    setPrior(flag) {
        if (flag != null) {
            this.usePrior = flag;
        } else {
            this.usePrior = true;
        }
    }
    
    // Do we extend the final orbit subsequent
    // to its stop time?  If not the
    // orbit will return position adn velocity
    // as 0.
    setSubseq(flag) {
        if (flag != null) {
            this.useSubseq = flag;
        } else {
            this.useSubseq = true;
        }
    }
    
    // Make sure we use the correct orbit[s] for this
    // time
    setOrbit(time) {
        
        if (this.norbit < 0) {
            throw new Error("No elements in orbit chain");
        }
        this.isPrior  = time < this.startTime;
        this.isSubseq = time > this.stopTime;
        
        
        while (time > this.orbits[this.index].stop) {
            if (this.index < this.norbit-1) {
                this.index += 1;
            } else {
                break;
            }
        }
        while (time < this.orbits[this.index].start) {
            if (this.index > 0) {
                this.index -= 1;
            } else {
                break;
            }
        }
        // Should we be interpolating?
        this.interp =  (time > this.orbits[this.index].interp && this.index < this.norbit-1);
    }
    
    
    // The the position of the item at the specified time.
    position(time) {
        const interp = this.setOrbit(time);
        let r0;
        
        // If beyond the range of the chain, do we still
        // want to use these orbits?
        if ( (this.isPrior  && !this.usePrior) ||
             (this.isSubseq && !this.useSubseq) ) {
            r0 = [0,0,0];
        
        // Just use the current (first or last) orbit if prior or subseqent.
        // (since we've already checked that we don't want to use the origin then)
        } else if (this.isPrior || this.isSubseq) {
            r0 = this.orbits[this.index].orbit.position(time);
            
        } else {
        
            if (interp) {
                let curr = this.orbits[this.index];
                // OK need to interpolate between two orbits.
                let f1 = (time - curr.interp)/(curr.stop-curr.interp);
                let f0 = 1-f1;
                    r0 = curr.orbit.position(time);
                    let r1 = this.orbits[this.index+1].orbit.position(time);
                    r0 = [f0*r0[0] + f1*r1[0], 
                          f0*r0[1] + f1*r1[1], 
                          f0*r0[2] + f1*r1[2]
                         ];
            } else {
                let orb = this.orbits[this.index];
                r0 = orb.orbit.position(time);
            }
        }
        
        if (this.orbits[this.index].scale) {
            r0 = Util.times(this.orbits[this.index].scale, r0);
        }
        return r0;
    }
    
    static createChain(name, chainDescrip) {
        
        let chain = new ChainedOrbit();
        
        chain.name = name;
        
        if (chainDescrip.prior) {
            chain.setPrior(chainDescrip.prior);
        }
        if (chainDescrip.subseq) {
            chain.setSubseq(chainDescrip.subseq);
        }
        Util.log("Creating orbit chain for "+name);
        
        for (let i=0; i<chainDescrip.orbits.length; i += 1) {
            
            let orb    = chainDescrip.orbits[i];
            let scale = orb.scale? orb.scale: 1;
            
            let qorb = new QOrb(orb.sat);
            qorb.scale = 1/Constants.AU;
            if (orb.planet) {
               let porb = new QOrb(orb.planetOrb);
               porb.scale = 1/Constants.AU;
               qorb = new SecondaryOrbit(porb, qorb, -1);
            } else {
                if (qorb.e > 1) {
                    qorb.scale *= -1;
                }
                if (orb.sat.scale) {
                    qorb.scale *= orb.sat.scale;
                }
            }
            
            chain.addOrbit(qorb, orb.start, orb.stop, orb.interp, scale);
        }
        Util.log("  Chain created with "+chainDescrip.orbits.length+" orbits");
        return chain;
    }
}

export default ChainedOrbit;