import Util from      "../std/Util.js";
import Constants from "../std/Constants.js";
import Orbit from     "../orbits/Orbit.js";
import ParameterFactory from "../orbits/ParameterFactory.js";


const loadMPCData = function (url, startTime, callback) {
    Util.log("Initiating loading of MPC data");
    let xhr  = new XMLHttpRequest();
    xhr.open('GET', url, true);
    xhr.onload  = function() {
        let stars = xhr.responseText;
        let json = JSON.parse(stars);
        let zz;
        for (zz in json) {
            break;
        }
        parseMPCData(startTime, callback, json[zz]);
    };
    
    xhr.onerror = function() {
        Util.log("Called error function when loading asteroids and comets!");
    }
    
    xhr.send(null);
}

// Called as callback in loadMPCData
const parseMPCData = function(startTime, callback, mpcdata) {
    const info  = [];
    var nskip       = 0;
    var nhyper      = 0;
    var nAst        = 0;
    var nCom        = 0;
    var unknowns = 0;
    Util.log("Number of MPC small bodies:"+mpcdata.length)
    for (var i=0; i<mpcdata.length; i += 1) {
        const elements = mpcdata[i];
        var name = elements.Name;
        if (!name) {
            name = elements.Designation_and_name;
        } 
        if (!name) {
            if (elements.Number) {
                name = '#'+elements.Number;
            } else {
                unknowns += 1;
                name = "Unnamed_"+unknowns;
            }
        }

        const orb      = new Orbit(name);

        const e = elements.e;
        const otype = elements.Orbit_type;
        let type = "asteroid";
        if (e == null || e === 1) {
            // Skipping parabolic and radial orbits for the nonce
            nskip += 1;
            continue;
        }

        if (e > 1) {
            nhyper += 1;
        }


        orb.addParameter(ParameterFactory.genParam("Eccen", e));
        orb.addParameter(ParameterFactory.genParam("i", Util.toRadians(elements.i)));
        orb.addParameter(ParameterFactory.genParam("nu", Util.toRadians(elements.Peri)));
        orb.addParameter(ParameterFactory.genParam("Omega", Util.toRadians(elements.Node)));

        // Comets give the time of perihelion and perihelion distance, 
        // while asteroids give the Mean anomaly at epoch and semimajor axis.
        if (otype === "C" || otype === "P"  || otype === "A"  || otype === "I") {
            type = "comet";

            const year = elements.Year_of_perihelion;
            const mon  = elements.Month_of_perihelion;
            const day  = elements.Day_of_perihelion;
            const d    = new Date(year, mon-1, Math.floor(day));
            var t1970  = d.setDate(Math.floor(day))/1000;
            t1970     += 86400 * (day-Math.floor(day));  // Seconds in unix epoch.
            var t0     = t1970 - Constants.Epoch0;
            orb.addParameter(ParameterFactory.genParam("t0", t0));
            var peridist = elements.Perihelion_dist*Constants.AU;            
            orb.addParameter(ParameterFactory.genParam("a", peridist/(1 - e)));
            nCom += 1;

        } else {

            orb.addParameter(ParameterFactory.genParam("a", elements.a*Constants.AU));
            const epoch = (elements.Epoch - Constants.JD0)*86400;
            // To find t0, we take the mean anomaly at the epoch and use the mean motion to compute
            // the number of days we are past.
            const delta = (elements.M / elements.n)*86400;
            orb.addParameter(ParameterFactory.genParam("t0", epoch-delta));
            nAst += 1;
        }

        orb.addParameter(ParameterFactory.genParam("t", startTime));
        const r = orb.getParameter("r", true);

        if (r == null) {
            nskip += 1;
            continue;
        }
        const qorb = orb.quickOrbit();
        if (qorb == null) {
            Util.log("Found null orbit at index:"+i);
        }

        info.push({type: type, Orbit_type: otype, name: name, orbit: orb.quickOrbit(), H:elements.H, Number: elements.Number});
    }
    
    Util.log("Parsed MPC data");
    Util.log("    "+info.length +" total bodies");
    Util.log("    "+nAst        +" asteroids");
    Util.log("    "+nCom        +" comets");
    Util.log("    "+nhyper      +" hyperbolic orbits");
    Util.log("    "+nskip       +" orbits skipped (parabolic or missing information)");
    callback(info);    
}


export default loadMPCData;