import  {Component, createRef}  from "react";
import Util             from "../std/Util.js";
import Constants        from "../std/Constants.js";
import ParameterFactory from "../orbits/ParameterFactory.js";
import Parameter        from "../orbits/Parameter.js";
import Orbit            from "../orbits/Orbit.js";

import UnitLegend       from "./UnitLegend.js";

import Row       from "./Row.js";
import status    from "./Status.js";
import TextInput from "../elems/TextInput.js";

class OrbitCalculator extends Component {
    
    constructor(props) {
        super(props);
        this.params = ParameterFactory.legalParameters;
        this.orbit  = null;
        this.fixed = null;
        this.varies = null;
        this.fixedRef = [];
        this.variesRef = [];
        this.orbitName = null;
        this.animFactory = null;
        this.displayCount = 0;
    }

    // It seems like the first time we create the objects we 
    // can get proper references to them, but later entries don't
    // seem to work.  So we only compute the references the first
    // time.  Probably something dodgy going on here.
    render() {
        const here = this;
        this.varies = [];
        this.fixed  = [];
        var doRef = true;
        if (this.fixedRef.length > 0) {
            doRef = false;
        }
        for (var name in this.params) {
            const param = this.params[name];
            var ref;
            var elem;
            if (doRef) {
                ref = createRef();
                elem = <Row calc={this} ref={ref} param={param} />;
            } else {
                elem = <Row calc={this} param={param} />;
            }
            if (param.varies) {
                this.varies.push(elem);
                if (doRef) {
                    this.variesRef.push(ref);
                }
            } else {
                this.fixed.push(elem);
                if (doRef) {
                    this.fixedRef.push(ref);
                }
            }
        }        
        return (
<div>
    Name for orbit:&nbsp; 
    <TextInput placeholder="Optional name"
       callback={function(name) {here.setOrbitName(name);}} />
    <p />
    Status: <div id="statusContent"></div>
    <p />
    
  <table><tbody>
    <tr key="hdr"><th>Name</th><th>Value</th><th>Units</th><th align="left">Description</th></tr>
    <tr key="div1"><td colSpan='4' align='center'> <i>Orbit Constants</i></td></tr>
    {this.fixed}
    <tr key="div2"><td colSpan='4' align='center'><i>Orbit Variables</i></td></tr>
    {this.varies}
    </tbody>
  </table>
  <table><tbody>
            <tr>
            <td key="1"><button
              onClick={this.compute.bind(this)}
            >Compute</button></td>
            <td key="2"><button
              onClick={this.displayOrbit.bind(this)}
            >Render orbit</button></td>
            <td key="3"><button
               onClick={this.clearAll.bind(this)}
            >Clear all</button></td>
            <td key="4"><button
               onClick={this.clearVarying.bind(this)}
            >Clear variables only</button></td>
            </tr>
      </tbody>
    </table>
    <p />
    <hr/>
    <UnitLegend />
    </div>    
    );
    }
    
    // This may be from an invoking page.
    setAnimFactory(animFactory) {
        this.animFactory = animFactory;
    }
    
    clearAll() {
        this.clear(this.fixedRef);
        this.clear(this.variesRef);
        this.orbit = null;
        Util.log("Clearing orbit...", this.orbit);
    }
    
    clearVarying() {
        this.clear(this.variesRef);
    }
    
    clear(rows) {
        for (var i=0; i<rows.length; i += 1) {
            const row = rows[i].current;
            
            if (row.state && row.state.value) {
                row.setState({value: "", computed: false});
            }
            row.parameter = null;
            row.validated = false;
        }
    }
    
    compute() {
        // Compute all of the parameters that we can given the 
        // current inputs.
        
        // First make sure all current inputs are valid.
        const all = this.fixedRef.concat(this.variesRef);
        Util.log("In compute:", this.orbit);
        if (!this.orbit) {
            this.orbit = new Orbit(this.orbitName);
        }
        let orbit = this.orbit;
        Util.log("In compute now:", this.orbit, orbit);
        if (!this.checkOrbitParams(orbit, all) ) {
            return;
        }
        
        var updated = true;
        var totalUpdates = 0;
        while (updated) {
            updated = false;
            // Loop overa all parameters and see if we can update them.
            for (var i=0; i<all.length; i += 1) {
                const row = all[i].current;
                if (this.tryRowUpdate(row, orbit)) {
                    updated       = true;
                    totalUpdates += 1;
                }
            }
        }
        this.orbit = orbit;
        status(totalUpdates+" parameters computed");
    }
    
    checkOrbitParams(orbit, all) {
        Util.log("Checking: ",orbit);
        for (var i=0; i<all.length; i += 1) {                    
            const row = all[i].current;
            Util.log("Looking at param:"+row.parameter);
            if (row.state && row.state.value) {
                if (!row.validated) {
                    Util.log("Validating row");
                    row.validate(row.state.value);
                    if (!row.validated) {
                        status("Invalid entry for parameter:"+row.param.name);
                        return false;
                    }
                }
                Util.log("Looking at row state:", i, row.state);
                Util.log("Looking at row value:", i, row.state.value);
                Util.log("Looking at row.validated:", row.validated);
                Util.log("Before add1:"+orbit);
                Util.log("Before add:2"+orbit.addParameter);
                Util.log("Before add:3"+row);
                Util.log("Before add:4"+row.parameter);
                orbit.addParameter(row.parameter);
            }
        }
        status("");
        return true;
    }
        
    tryRowUpdate(row, orbit) {
        if (orbit == null) {
            orbit = new Orbit(this.orbitName);
            this.orbit = orbit;
            const all = this.fixedRef.concat(this.variesRef);

            if (!this.checkOrbitParams(orbit, all)) {
                return;
            }
        }

        let newPar = null;
        if (!row.parameter) {
            const name   = row.param.name;
            newPar = orbit.getParameter(name, true);
            if (newPar != null) {
                row.setParameter(newPar);
            }
        }
        return newPar;
    }
    
    displayOrbit() {
        this.compute();
        this.displayCount += 1;
        var qorb = this.orbit.quickOrbit();
        if (qorb == null) {
            status("Insufficient known orbital parameters to determine orbit");
        } else {
            var name = this.orbit.getOrbitName();
            if (!name) {
                name = "UserOrbit";
            }
            
            name += this.orbitCount;
            
            // Use the central mass to decide where to show the orbit.
            var mass = this.orbit.getParameter("M").value;
            var mrat = mass/Constants.MSun;
            let anim = null;
            if (mrat > 0.999999 && mrat < 1.000001) {
                anim = "Solar System";
            } else {
                mrat = mass/Constants.MEarth;
                if (mrat > 0.999999 && mrat < 1.000001) {
                    amim = "The Earth and environs";
                }                
            }
            
            if (anim) {
                this.animFactory.getAnimation(anim).addExternalOrbit(name, qorb);
            } else {
                status("Central orbit mass not consistent with Earth or sun")
            }
        }        
    }
    
    setOrbitName(name) {
        this.orbitName = name;
    }
}

export default OrbitCalculator;