/** This function solves Kepler's equation to find
 *  the eccentric anomaly as a function of the mean anomaly.
// For orbits with eccentricity near 1 we need
// to be careful when MA is near 0 or Pi.  There is a vast art on this
// problem but for the nonce we are using a simple but likely inefficient
// approach.  Note the functions defined.  Nominally this should work
// for both elliptical and hyperbolic orbits.
 * 
 */
 
import Constants from "../std/Constants.js";
import Util      from "../std/Util.js";

const Kepler = function(ma, ecc) {
    
    if (ecc < 0) {
        throw new Error("Illegal argument to kepler:"+ma+" "+ecc);
    }
    
    // For periodic orbits set the range to 0 to 2*PI.
    if (ecc < 1) {
        if (ma < 0 || ma > 2*Math.PI) {
            ma = ma%(2*Math.PI);
            if (ma < 0) {
                ma += 2*Math.PI;
            }
        }
    }
    
    

    // Mean anomaly from eccentric anomaly and eccentricity
    function fma(ea, ecc) {

        var val;
        if (ecc > 1) {
            val = ecc * Math.sinh(ea) - ea;
        } else {
            val = ea - ecc * Math.sin(ea);
        }
        return val;
    }

    // Derivative of ma with respect to ea.
    function fdma(ea, ecc) {
        if (ecc > 1) {
            return (ecc * Math.cosh(ea) - 1);
        } else {
            return (1 - ecc * Math.cos(ea));
        }
    }

    if (ecc < Constants.SHAPE_RATIO) {
        // Circular orbit, so ma = ea = ta
        return ma;
    }

    // Now we just iterate till we converge.  We know
    // that if ma is between 0-pi, or pi-2pi, then ea is
    // in same range so we can limit overshoots.
    // Use initial guess

    var emin, emax;
        // Somewhat to my surprise using just ma as the initial
        // value seemed to be fastest although using ma/(1-e) for
        // small ma seems like it should be better.  That does
        // do slightly fewer evaluations, but overall seems to
        // take longer.  There may be something
        // else that needs to be fixed.    
    var eaTest = ma;
    if (ecc < 1) {
        emin = 0;
        if (ma > Math.PI) {
            emin = Math.PI;
        }
        emax = emin + Math.PI;
        
    } else {
        // The maximum value for the true anomaly is acos(-1/e), but
        // the eccentric anomaly is not constrained,
        // so we set the limits to rather large values.
        
        if (ma <= 0) {
            emin = -1.e10;
            emax = 0;
        }  else {
            emin = 0;
            emax =  1.e10;
        }
        if (Math.abs(ma) > 2*Math.PI) {
            eaTest = Math.asinh(ma/ecc);            
        }
    }

    if (eaTest > emax) {
        eaTest = emax;
    }
    if (eaTest < emin) {
        eaTest = emin;
    } 

    var maTest = fma(eaTest, ecc);
    var dmadea = fdma(eaTest, ecc);

    var iter = 0;

    while (true) {
        
        
        iter += 1;
        if (iter > 1000) {
            Util.log("EA calculation: Too many iterations:" + ecc + " " + ma);            
            return null;
        }
        var delta = ma - maTest;

        if (Math.abs(delta) < Constants.HALT_RATIO) {
            return eaTest;
        }
        // May need to be more generous if MA is very large for hyperbolic orbit.
        if (Math.abs(ma)  > 100 ) {
            if (Math.abs(delta/ma) < Constants.HALT_RATIO) {
                return eaTest;                
            }
        }
        dmadea    = fdma(eaTest, ecc);


        if (delta > 0) {
            emin = eaTest;
        } else {
            emax = eaTest;
        }

        var eaDelta = delta / dmadea;

        var maNew;
        var eaNew;
        var smaller = false;  // Flag if we jump too far.

        // Loop to adjust the size we jump on this iteration.
        // We may be either jumping too far such that
        // the delta is increasing, or too little so that
        // we don't significantly reduce the delta.
        var subiter = 0;
        while (true) {
            subiter += 1;
            if (subiter > 100) {
                return null;
            }

            // Make sure we don't go past the firm boundaries
            // for the EA.
            eaNew = eaTest + eaDelta;
            if (eaNew > emax) {
                eaNew = emax;
            } else if (eaNew < emin) {
                eaNew = emin;
            }
            

            // Try the Newton's method value.
            maNew = fma(eaTest + eaDelta, ecc);
            var deltaNew = ma - maNew;


            // If we decreased the delta by a lot, then this is good enough.
            if (Math.abs(deltaNew / delta) < 0.5) {
                break;
            }

            // The delta got worse, we jumped too far.
            if (Math.abs(deltaNew / delta) > 1) {

                // Moved too far...
                eaDelta /= 2;
                smaller = true;

            } else {

                // The delta got smaller, but not by very much.
                // Maybe we could try to jump a little further?

                if (smaller) {
                    // Not this time...
                    // In a prior iteration we'd been reducing the size of the jump
                    // because it was actually increasing delta.  Now
                    // that we've begun decreasing delta we'll accept this
                    // value for this iteration.  If we need to we can
                    // try increasing it next iteration.
                    break;
                }
                // Test doubling eaDelta and see if it's better
                // If so we'll stay in this loop with the value.  If not
                // we break and use the old value for eaDelta.
                var maX = fma(eaTest + 2 * eaDelta, ecc);
                if (Math.abs(maX - ma) < Math.abs(maNew - ma)) {
                    eaDelta *= 2;
                } else {
                    break;
                }
            }
        }
        
        eaTest = eaNew;
        maTest = maNew;
        
    }
};

export default Kepler;
